<template>
  <div id="collectionBar">
    <el-menu default-active="1" class="el-menu-vertical-demo" @select="handleSelect" background-color="#f5f2f0"
      text-color="#9093a5" active-text-color="#1e1e1e">

      <el-menu-item index="1" v-if="!showBar">
        <span slot="title">{{ FavoritesForm.favorites }}</span>
      </el-menu-item>
      <div v-if="showBar">
        <el-menu-item :index="index + 1" v-for="(collection, index) in FavoritesForm" :key="collection">
          <span slot="title">{{ collection.favorites }}</span>
        </el-menu-item>
      </div>
    </el-menu>
  </div>
</template>
  
<script>
import request from '../api/request'
export default ({
  name: 'CodeUserCollectionBar',
  components: {
  },
  data() {
    return {
      FavoritesForm: {
        favorites: '默认收藏夹'
      },
      showBar: false,
    };
  },
  mounted() {
    this.getFavorites();
  },
  methods: {
    //列表
    getFavorites() {
      //发送ajax请求
      return request({
        method: 'get',
        url: "/users/getFavorites", params: { username: this.$cookies.getCookie("username") }
      }).then((res) => {
        console.log(this.FavoritesForm.length > 1);
        if (this.FavoritesForm.length > 1) {
          this.FavoritesForm = res.data.data;
          this.showBar = true;
        }
      });
    },
    handleSelect(key) {
      // console.log(key);
      // this.$message.success(this.FavoritesForm[key - 1].favorites);
      if( this.FavoritesForm.length > 1 ){
        this.$router.push({ name: 'getcollections', params: { fname: this.FavoritesForm[key - 1].favorites } });  
      }else{
        this.$router.push({ name: 'getcollections', params: { fname: this.FavoritesForm.favorites } });  
      }
      // this.$router.go(0);
    }
  }
})
</script>
  
<style>
#collection {
  width: 100%;
  height: 100%;
}
</style>
  