import Vue from 'vue'
import VueRouter from 'vue-router'
import CodeLogin from '../login_register/CodeLogin.vue'
import CodeRegister from '../login_register/CodeRegister.vue'
import CodeIndex from '../components/CodeIndex.vue'
import CodeSuserLogin from '../login_register/CodeSuserLogin.vue'
import CodeSuserIndex from '../components/CodeSuserIndex.vue'
import CodeSelectCenter from '../components/CodeSelectCenter.vue'
import CodeUserIndex from '../components/CodeUserIndex.vue'
import CodeUserInfo from '../components/CodeUserInfo.vue'
import CodeUserAccount from '../components/CodeUserAccount.vue'
import CodeBase from '../components/CodeBase.vue'
import CodeUserTempArticle from '../components/CodeUserTempArticle.vue'
import CodeUserUpedArticle from '../components/CodeUserUpedArticle.vue'
import CodeSuserCreateNotice from '../components/CodeSuserCreateNotice.vue'
import CodeSuserUnNoticed from '../components/CodeSuserUnNoticed.vue'
import CodeSuserNoticed from '../components/CodeSuserNoticed.vue'
import CodeArticleShowCenter from '../components/CodeArticleShowCenter.vue'
import CodeUserEditArticle from '../components/CodeUserEditArticle.vue'
import CodeArticlePreview from '../components/CodeArticlePreview.vue'
import CodeUserHistory from '../components/CodeUserHistory.vue'
import CodeUserCollections from '../components/CodeUserCollections.vue'
import CodeUserCollectionResoult from '../components/CodeUserCollectionResoult.vue'
import CodeHome from '../components/CodeHome.vue'
import cookies from "../cookie.js";
Vue.use(VueRouter)

// 获取原型对象push函数
const originalPush = VueRouter.prototype.push

// 获取原型对象replace函数
const originalReplace = VueRouter.prototype.replace

// 修改原型对象中的push函数
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

// 修改原型对象中的replace函数
VueRouter.prototype.replace = function replace(location) {
  return originalReplace.call(this, location).catch(err => err)
}

//创建router实例对象，去管理一组一组的路由规则
const router = new VueRouter({
  // mode: 'history', // 去除#
  // base: '/CodeCommunity/',
  routes: [
    {
      path: '/',
      component: CodeIndex,
      children: [ //通过children配置子级路由
        {
          name: 'indexshow',
          path: 'home',
          meta: { title: "社区首页" },
          component: CodeHome,
        },
        {
          name: 'articleshow',
          path: 'home/article/:aid',
          component: CodeArticleShowCenter,
          props: true
        },
        {
          name: 'selectby',
          path: 'home/selectby/:sby',
          meta: { title: "帖子搜索" },
          component: CodeSelectCenter,
          props: true
        },
      ]
    },
    {
      name: 'login',
      path: '/login',
      meta: { title: "社区登录" },
      component: CodeLogin,
    },
    {
      name: 'register',
      path: '/register',
      meta: { title: "社区注册" },
      component: CodeRegister,
    },

    {
      name: 'usercenter',
      path: '/usercenter',
      component: CodeUserIndex,
      children: [ //通过children配置子级路由
        {
          name: 'userinfo',
          path: 'userinfo',
          meta: { title: "个人信息" },
          component: CodeUserInfo
        },
        {
          name: 'account',
          path: 'account',
          meta: { title: "账号设置" },
          component: CodeUserAccount
        },
        {
          name: 'codebase',
          path: 'codebase',
          meta: { title: "代码仓库" },
          component: CodeBase
        },
        {
          name: 'gethistory',
          path: 'gethistory',
          meta: { title: "浏览记录" },
          component: CodeUserHistory
        },
        {
          path: 'getfavorites',
          component: CodeUserCollections,
          children: [ //通过children配置子级路由
            {
              name: 'getcollections',
              path: 'getcollections/:fname',
              meta: { title: "我的收藏" },
              component: CodeUserCollectionResoult,
              props: true
            },
          ]
        },
        {
          name: 'temparticle',
          path: 'temparticle',
          meta: { title: "我的草稿" },
          component: CodeUserTempArticle
        },
        {
          name: 'upedarticle',
          path: 'upedarticle',
          meta: { title: "我的帖子" },
          component: CodeUserUpedArticle
        },
        {
          name: 'editarticle',
          path: 'editarticle',
          meta: { title: "编辑帖子" },
          component: CodeUserEditArticle
        },
        {
          name: 'editarticlebyid',
          path: 'editarticlebyid/:maid',
          meta: { title: "修改帖子" },
          component: CodeUserEditArticle,
          props: true
        },
        {
          name: 'articlepreview',
          path: 'articlepreview',
          meta: { title: "预览帖子内容" },
          component: CodeArticlePreview,
        },
      ]
    },


    {
      name: 'slogin',
      path: '/slogin',
      meta: { title: "管理员登录" },
      component: CodeSuserLogin,
    },
    {
      path: '/s',
      component: CodeSuserIndex,
      children: [ //通过children配置子级路由
        {
          name: 'editnotice',
          path: 'editnotice',
          meta: { title: "公告编辑" },
          component: CodeSuserCreateNotice
        },
        {
          name: 'editnoticebyid',
          path: 'editnotice/:nid',
          meta: { title: "公告修改" },
          component: CodeSuserCreateNotice,
          props: true
        },
        {
          name: 'unnoticed',
          path: 'unnoticed',
          meta: { title: "待发布公告" },
          component: CodeSuserUnNoticed
        },
        {
          name: 'noticed',
          path: 'noticed',
          meta: { title: "已发布公告" },
          component: CodeSuserNoticed
        },
      ]
    },
  ]
})

router.beforeEach((to, from, next) => {
  /* 路由发生变化修改页面title */
  if (to.meta.title) {
    document.title = to.meta.title;
  }
  if (to.path == '/usercenter' || to.path == '/'
    || to.name == 'account' || to.name == 'codebase'
    || to.name == 'temparticle' || to.name == 'upedarticle'
    || to.name == 'userinfo' || to.name == 'editarticle'
    || to.name == 'articlepreview' || to.name == 'editarticlebyid'
    || to.name == 'gethistory' || to.name == 'getcollections'
  ) {
    if (to.path == '/') {
      next();
      router.push({ name: "indexshow" });
    }
    let login = cookies.checkCookie();
    if (login) {
      if (to.path == '/usercenter') {
        next();
        router.push({ name: "userinfo" });
        // } else if (to.name == 'articlepreview') {
        //   if (this.$store.state.patitle == 'null' && this.$store.state.pacontent == 'null') {
        //     alert("不能直接通过路径预览帖子内容！")
        //   } else {
        //     next();
        //   }
      } else if(to.name == 'getcollections'){
        next();
        router.push({ name: "getcollections", params:{fname: '默认收藏夹'} });
      } else {
        next();
      }
    } else {
      router.push({ path: "/login" });
    }
  } else if (to.path == '/s' || to.name == 'unnoticed'
    || to.name == 'noticed' || to.name == 'editnotice'
    || to.name == 'editnoticebyid'
  ) {
    let slogin = cookies.checkSuserCookie();
    if (slogin) {
      if (to.path == '/s') {
        next();
        router.push({ name: "unnoticed" });
      } else {
        next();
      }
    } else {
      router.push({ path: "/slogin" });
    }
  } else {
    next();
  }
})
//暴露router
export default router
