<template>
  <div id="aright">
    <div id="topShow">
      <div id="articleTitle">{{ showArticle.editTitle }}</div>
      <br>
      <table class="articleTitle">
        <tr>
          <td>
            作者：{{ showArticle.username }}&nbsp;&nbsp;
          </td>
          <td>
            <i class="el-icon-time"></i>&nbsp;于 {{ showArticle.createTime }} 发布&nbsp;&nbsp;
          </td>
          <td>
            <i class="el-icon-view"></i>&nbsp;{{ showArticle.reading }}&nbsp;&nbsp;
          </td>
          <td>
            <img class="star" src="../assets/staroff.png" v-if="!shows" @click="stardialog()" />
            <img class="star" src="../assets/staron.png" v-if="shows" @click="stardialog()" />
            <el-dialog title="收藏管理" :visible.sync="dialogTableVisible" width="30%" center>
              <el-table :data="FavoritesForm">
                <el-table-column type="index" label="序号" width="55" align="center"></el-table-column>
                <el-table-column prop="favorites" label="收藏夹" align="center"></el-table-column>
                <el-table-column label="操作" width="100" align="center">
                <template slot-scope="scope">
                    <el-button size="mini" type="danger" round @click="isCollect(scope.row)" v-if="collectionbtn">已收藏</el-button>
                    <el-button size="mini" round @click="isCollected(scope.row)" v-if="!collectionbtn">收藏</el-button>
                </template>
            </el-table-column>
              </el-table>
            </el-dialog>
          </td>
          <td>&nbsp;{{ showArticle.collections }}</td>
        </tr>
      </table>
    </div>

    <div id="articleShow">
      <div id="lfShow">
        <div id="editor-content-view"></div>
      </div>
    </div>

    <!-- <table class="bottomShow">
      <tr>
        <td class="userlogo">
          <el-link @click="toUserIndex()" :underline="false">
            <el-avatar icon="el-icon-user-solid" class="userlogo" :src="ImgUrl"></el-avatar></el-link>
        </td>
        <td class="user">
          {{ showArticle.username }}&nbsp;
          <el-button size="mini" v-if="!btnShow" round @click="isattention()">关注</el-button>
          <el-button size="mini" v-if="btnShow" round @click="isattention()">已关注</el-button>
        </td>
        <td class="rightbottom">
          <img class="star" src="../assets/staroff.png" v-if="!shows" @click="isCollect()" />
          <img class="star" src="../assets/staron.png" v-if="shows" @click="isCollect()" />
        </td>
      </tr>
    </table> -->
    <table class="comment">
      <tr>
        <td class="pl">
          <el-input type="textarea" :autosize="{ minRows: 3 }" placeholder="你的评论..."
            v-model="comments.content"></el-input>
          <br><el-link type="info" @click="submitComment()">提交评论</el-link>
        </td>
      </tr>
    </table>
  </div>
</template>
  
<script>
import request from '../api/request'
import { ipurl } from '../api/ipurl'
import Prism from 'prismjs'
import 'prismjs/themes/prism.css'
export default ({
  name: 'CodeArticleRight',
  components: {
  },
  data() {
    return {
      aid: this.$router.currentRoute.params.aid,
      ImgUrl: '',
      shows: false,
      btnShow: false,
      usernames: this.$cookies.getCookie("username"),
      comments: {},
      historys: {
        article_id: '',
        username: this.$cookies.getCookie("username"),
      },
      Collections: {
        articleId: '',
        username: this.$cookies.getCookie("username"),
        favorites: '默认收藏夹'
      },
      Collection: {
        articleId: '',
        username: this.$cookies.getCookie("username"),
        favorites: '默认收藏夹'
      },
      dCollections: {
        articleId: '',
        username: this.$cookies.getCookie("username"),
        favorites: '默认收藏夹'
      },
      showArticle: {
        editTitle: '',
        editContent: '',
        username: '',
        createTime: '',
        reading: 0,
        collections: 0
      },
      dialogTableVisible: false,
      FavoritesForm: [{
        favorites: '默认收藏夹'
      }],
      collectionbtn: false,
    }
  },
  mounted() {
    if (isNaN(parseInt(this.aid))) {
      console.log(parseInt(this.aid));
      alert('路径值错误！');
      this.$router.push({ path: "/home" });
    } else {
      this.selectArticleById();
      this.historys.article_id = this.aid;
      this.Collections.articleId = this.aid;
      this.Collection.articleId = this.aid;
      this.dCollections.articleId = this.aid;
    }
  },
  methods: {
    //列表
    getFavorites() {
      //发送ajax请求
      return request({
        method: 'get',
        url: "/users/getFavorites", params: { username: this.$cookies.getCookie("username") }
      }).then((res) => {
        if (res.data.data.length > 0) {
          this.FavoritesForm = res.data.data;
          // console.log(this.FavoritesForm);
        }
      });
    },
    submitComment() {
      this.$message.success("评论成功！");
      this.comments = {};
    },
    isattention() {
      this.btnShow = !this.btnShow;
    },
    stardialog() {
      if (this.usernames != null) {
      this.getFavorites();
      this.dialogTableVisible = true;
    } else {
        // 跳转到登录窗口
        let routeData = this.$router.resolve({ name: 'login' });
        window.open(routeData.href, '_blank');
      }
    },
    isCollected() {
        return request({
          method: 'post',
          url: "/users/addCollections",
          data: this.Collections
        }).then((res) => {
          if (res.data.code == 60381) {
            this.shows = true;
            this.collectionbtn = true;
            this.showArticle.collections = this.showArticle.collections + 1;
            this.$message.success("收藏成功！");
          } else if (res.data.code == 60380) {
            this.$message.error(res.data.msg);
          } else {
            this.$message.error(res.data.msg);
          }
        });
    },

    isCollect() {
      return request({
        method: 'post',
        url: "/users/deleteCollections",
        data: this.dCollections
      }).then((res) => {
        if (res.data.code == 60381) {
          this.shows = false;
          this.collectionbtn = false;
          this.showArticle.collections = this.showArticle.collections - 1;
          this.$message.info("取消收藏成功！");
        } else if (res.data.code == 60380) {
          this.$message.error(res.data.msg);
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },

    isCollections() {
      console.log(this.Collection);
      return request({
        method: 'post',
        url: "/users/isCollections",
        data: this.Collection
      }).then((res) => {
        if (res.data.code == 60381) {
          this.shows = true;
          this.collectionbtn = true;
        } else if (res.data.code == 60380) {
          this.shows = false;
          this.collectionbtn = false;
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },
    addRead() {
      if (this.usernames == null) {
        if (this.$cookies.getCookie(this.aid) == null) {
          this.$cookies.setArticleCookie(this.aid, this.aid, 60 * 60 * 12);
          return request({
            method: 'get',
            url: "/articles/addReadById", params: { id: this.aid }
          }).then((res) => {
            if (res.data.code == 60331) {
              this.showArticle.reading = this.showArticle.reading + 1;
            } else if (res.data.code == 60330) {
              this.$message.error(res.data.msg);
            } else {
              this.$message.error(res.data.msg);
            }
          });
        }
      } else {
        if (this.$cookies.getCookie(this.usernames + this.aid) != this.aid) {
          this.$cookies.setArticleCookie(this.usernames + this.aid, this.aid, 60 * 60 * 12);
          return request({
            method: 'get',
            url: "/articles/addReadById", params: { id: this.aid }
          }).then((res) => {
            if (res.data.code == 60331) {
              this.showArticle.reading = this.showArticle.reading + 1;
            } else if (res.data.code == 60330) {
              this.$message.error(res.data.msg);
            } else {
              this.$message.error(res.data.msg);
            }
          });
        }
      }

    },
    addHistory() {
      //console.log(this.historys);
      if (this.$cookies.getCookie("username") != null) {
        return request({
          method: 'post',
          url: "/articlehistory/addHistory",
          data: this.historys
        }).then((res) => {
          console.log(res.data);
        });
      }
    },
    selectArticleById() {
      console.log(parseInt(this.aid));
      return request({
        method: 'get',
        url: "/articles/" + this.aid
      }).then((res) => {
        if (res.data.code == 60091) {
          this.showArticle = res.data.data;
          document.title = this.showArticle.editTitle;
          document.getElementById('editor-content-view').innerHTML = this.showArticle.editContent;
          Prism.highlightAll();
          this.ImgUrl = ipurl + '/files/download?fileName=UserImg.jpg&bucket=' + this.showArticle.username;
          sessionStorage.setItem("ausername", this.showArticle.username);
          setTimeout(() => {
            this.isCollections();
            this.addRead();
            this.addHistory();
          }, 100);
        } else if (res.data.code == 60090) {
          this.$message.error('未查到该帖子！');
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },
    toUserIndex() {
      this.$message.success(this.showArticle.username);
    },
  }
})

