<template>
  <div>
    <el-divider>密码设置</el-divider>
    <h3 style="padding-left: 30px;">密码</h3>
    <el-collapse @change="handleChange">
      <el-collapse-item title="修改密码" name="1" style="padding-left: 30px;">
        <div style="margin-left: 60px">
          <el-form model="pwdData">
            <table width="35%" height="100px">
              <tr>
                <td class="box">原密码</td>
                <td><el-input type="password" size="small" placeholder="请输入原始密码" name="oldpwd"
                    v-model="pwdData.oldpwd"></el-input></td>
              </tr>
              <tr>
                <td class="box">新密码</td>
                <td><el-input type="password" size="small" placeholder="请输入新密码" name="newpwd"
                    v-model="pwdData.newpwd"></el-input></td>
              </tr>
              <tr>
                <td class="box">确认密码</td>
                <td><el-input type="password" size="small" placeholder="请再次输入新密码" name="newpwd2"
                    v-model="pwdData.newpwd2"></el-input></td>
              </tr>
              <tr>
                <td colspan="2" align="center"><el-row>
                    <el-button type="primary" size="small" @click="checkpwd()">提交</el-button></el-row></td>
              </tr>
            </table>
          </el-form>
        </div>
      </el-collapse-item>
    </el-collapse>
    <el-divider></el-divider>
    <el-divider>邮箱设置</el-divider>
    <h3 style="padding-left: 30px;">邮箱</h3>
    <el-collapse @change="handleChange">
      <el-collapse-item title="修改邮箱" name="2" style="padding-left: 30px;">
        <div style="margin-left: 60px">
          <el-form model="emailData">
            <table width="35%" height="100px">
              <tr>
                <td class="box">邮箱</td>
                <td><el-input type="text" size="small" placeholder="请输入新邮箱" name="newemail"
                    v-model="emailData.newemail"></el-input></td>
              </tr>
              <tr>
                <td class="box">验证码</td>
                <td>
                  <el-input type="text" size="small" placeholder="请输入验证码" name="pwd" v-model="emailData.pwd"
                    style="width: 75%" class="boxr" clearable></el-input>
                  <el-button type="info" size="small" style="width: 25%" class="boxr" icon="el-icon-message"
                    @click="checkEmail()" v-if="!show">发送</el-button>
                  <el-button type="info" size="small" style="width: 25%" class="boxr" icon="el-icon-message" v-if="show"
                    disabled>{{ this.count }}秒</el-button>
                </td>
              </tr>
              <tr>
                <td colspan="2" align="center"><el-row>
                    <el-button type="primary" size="small" @click="modifyEmail()">提交</el-button></el-row></td>
              </tr>
            </table>
          </el-form>
        </div>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>
  
<script>
import request from '../api/request'
export default ({
  name: 'CodeUserAccount',
  components: {
  },
  data() {
    return {
      
      pwdData: {},
      emailData: {},
      receiveData: [],//验证码
      show: false,
      count: 0,

    };
  },
  methods: {
    handleChange(val) {
      console.log(val);
    },
    timeCount() {
      this.count = 60;
      this.show = true;
      this.timer = setInterval(() => {
        if (this.count > 0 && this.count <= 60) {
          this.count--;
          console.log("倒计时中。。。");
        } else {
          console.log("倒计时结束！");
          this.show = false;
          clearInterval(this.timer);
          this.timer = null;
        }
      }, 1000);
    },
    checkEmail() {
      if (this.emailData.newemail != null) {
        //发送ajax请求
        return request({
          method: 'get',
          url: "/users/checkemail", params: { email: this.emailData.newemail, label: "modify" }
        }).then((res) => {
          //console.log(res.data);
          if (res.data.code == 60071) {
            this.receiveData = res.data.data;
            this.$message.success(res.data.msg);
            this.timeCount();
          } else if (res.data.code == 60070) {
            this.$message.error(res.data.msg);
          } else {
            this.$message.error(res.data.msg);
          }
        });
      } else {
        alert("请输入邮箱！")
      }
    },

    //重置表单
    resetForm() {
      this.emailData = {};
      this.pwdData = {};
      this.receiveData = [];
      this.count = 0;

    },
    modifyEmail() {
      if (this.emailData.pwd == this.receiveData && this.emailData.pwd != null) {
        //发送ajax请求
        return request({
          method: 'get',
          url: "/users/setEmail", params: { username: this.$cookies.getCookie("username"), email: this.emailData.newemail }
        }).then((res) => {
          //console.log(res.data);
          if (res.data.code == 60131) {
            this.$message.success(res.data.msg);
            this.resetForm();
          } else if (res.data.code == 60130) {
            this.$message.error(res.data.msg);
          } else {
            this.$message.error(res.data.msg);
          }
        });
      } else {
        alert("验证码错误！")
      }
    },

    checkpwd() {
      if (this.pwdData.newpwd != null && this.pwdData.newpwd2 != null && this.pwdData.newpwd == this.pwdData.newpwd2 && this.pwdData.oldpwd != null) {
        //发送ajax请求
        return request({
          method: 'get',
          url: "/users/",
          params: {
            username: this.$cookies.getCookie("username"),
            password: this.pwdData.oldpwd
          }
        }).then((res) => {
          // console.log(res.data);
          if (res.data.code == 60011) {
            return request({
              method: 'get',
              url: "/users/setPwd",
              params: {
                username: this.$cookies.getCookie("username"),
                password: this.pwdData.newpwd
              }
            }).then((res) => {
              if (res.data.code == 60121) {
                this.$message.success(res.data.msg);
                this.resetForm();
                this.$cookies.deleteCookie('username', 'password', -1);
                setTimeout(() => {
                  this.$router.push({ path: '/login' });
                }, 1000);
              } else if (res.data.code == 60120) {
                this.$message.error(res.data.msg);
              } else {
                this.$message.error(res.data.msg);
              }
            });
          } else if (res.data.code == 60010) {
            this.$message.error("请输入正确的原始密码！");
          } else {
            this.$message.error(res.data.msg);
          }
        });
      } else {
        alert("两次新密码不一致或新密码为空！")
      }
    },
  }
})

</script>
    
<style>
.box {
  width: 20%;
  /*两端对齐*/
  margin: auto;
  font-size: 12px;
  text-align: justify;
  text-align-last: justify;
  text-justify: inter-ideograph;
  /*兼容ie*/
}

.boxr {
  float: left;
}
</style>
  