<template>
  <div id="ashows">
    <table class="alist" v-for="article in articleList1.slice((currentPage - 1) * pageSize, currentPage * pageSize)" :key="article">
      <tr>
        <td class="titles"><el-link :underline="false" style="font-size: 18px;font-weight: bold;"
            @click="toArticleShow(article)">{{ article.editTitle }}</el-link></td>
      </tr>
      <tr>
        <td class="contents">{{ article.editContenthtml }}</td>
      </tr>
      <tr>
        <td class="bottoms">
        <div class="lbottoms">
          <i class="el-icon-view"></i>&nbsp;{{ article.reading }}&nbsp;&nbsp;
          <img class="stars" src="../assets/staroff.png" />&nbsp;{{ article.collections }}&nbsp;
        </div>
        <div class="rbottoms">
        <el-link :underline="false" type="info" @click="toUserIndex(article)">
            {{ article.username }}</el-link> &nbsp;&nbsp; <font color="#9093a5">{{ article.createTime }}</font>
          </div>
        </td>
      </tr>
    </table>
    <el-pagination background layout="prev, pager, next,jumper, ->, total, slot" :total="articleList1.length"
            @current-change="handleCurrentChange" :current-page="currentPage" :page-size="pageSize"
            style="text-align: right;margin-top: 5px;">
        </el-pagination>
  </div>
</template>
  
<script>
import request from '../api/request'
export default ({
  name: 'CodeIndexArticles',
  components: {

  },
  data() {
    return {
      articleList1: {},
      selectInfo: this.$route.params.sby,
      currentPage: 1, //当前页 刷新后默认显示第一页
      pageSize: 20, //每一页显示的数据量 此处每页显示20条数据
    };
  },
  mounted() {
    // console.log(this.selectInfo);
    // console.log(this.$router.currentRoute.path);
    if(sessionStorage.getItem("selectInfo")!=null){
      this.selectInfo = sessionStorage.getItem("selectInfo");
    }
    if (this.$router.currentRoute.path == '/home') {
      this.getAllArticles1();
    } else {
      if (this.selectInfo == null || this.selectInfo == '') {
      this.getAllArticles1();
    } else {
      this.getAllArticles1by();
    }
    }
    
  },
  methods: {
    //点击按钮切换页面
    handleCurrentChange(currentPage) {
            this.currentPage = currentPage; //每次点击分页按钮，当前页发生变化
            // console.log(this.currentPage);
        },
    //列表
    getAllArticles1() {
      //发送ajax请求
      return request({
        method: 'get',
        url: "/articles/selectAllArticles"
      }).then((res) => {
        this.articleList1 = res.data.data;
        for (var i = 0; i < this.articleList1.length; i++) {
          this.articleList1[i].editContenthtml = this.articleList1[i].editContent.replace(/<[^>]+>/g, "");
        }
        console.log(this.articleList1);
      });
    },
    getAllArticles1by() {
      //发送ajax请求
      return request({
        method: 'get',
        url: '/articles/selectAllBy/' + this.selectInfo
      }).then((res) => {
        console.log(res.data.data);
        this.articleList1 = res.data.data;
        for (var i = 0; i < this.articleList1.length; i++) {
          this.articleList1[i].editContenthtml = this.articleList1[i].editContent.replace(/<[^>]+>/g, "");
        }
        console.log(this.articleList1);
      });
    },
    toArticleShow(article) {
      // this.$message.success(article.id);
      // 跳转到新窗口
      let routeData = this.$router.resolve({ name: 'articleshow', params: { aid: article.id } });
      window.open(routeData.href, '_blank');
    },
    toUserIndex(article) {
      this.$message.success(article.username);
    },

  }
})

</script>
  
<style>
#ashows {
  width: 98%;
  min-height: 1000px;
  height: auto !important;
  border-radius: 5px;
  margin-top: 20px;
  float: left;
}

.alist {
  width: 100%;
  height: 125px;
  border-radius: 5px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}

.titles {
  width: 98%;
  font-size: 18px;
  font-weight: bold;
  color: #1e1e1e;
  text-align: left;
  padding-top: 2px;
  padding-bottom: 2px;
  padding-left: 10px;
  padding-right: 10px;
  text-decoration: none;
}

/*
.color{color:#1e1e1e; text-decoration:none;font-weight:bold;}

.color:visited{color:#b8b8b8; text-decoration:none;font-weight:bold;}

.color:hover{color:#CF0000; text-decoration:underline;font-weight:bold;}
*/
.contents {
  width: 98%;
  font-size: 15px;
  color: #3c3c3d;
  text-align: left;
  /* 溢出隐藏 */
  overflow: hidden;
  /* 溢出隐藏使用省略号代替 */
  text-overflow: ellipsis;
  /* 展示方式 */
  display: -webkit-box;
  /* 行数限制 */
  -webkit-line-clamp: 2;
  /* 从上往下书写 书写方向*/
  -webkit-box-orient: vertical;
  padding-top: 2px;
  padding-bottom: 2px;
  padding-left: 10px;
  padding-right: 10px;

}

.bottoms{
  width: 98%;
  font-size: 15px;
  color: #3c3c3d;
  padding-top: 2px;
  padding-bottom: 2px;
  padding-left: 10px;
  padding-right: 10px;
  
}
.lbottoms {
  float: left;
}
.rbottoms {
  float: right;
}
.stars {
  width: 15px;
  height: 15px;
  margin-top: 5px;
}
</style>
  