<template>
    <div id="div1">
        <table width="100%" height="100%">
            <tr>
                <td width="33%"><router-link to="/home"><img src="../assets/logo.png" class="logo" /></router-link></td>
                <td width="33%">
                    <table width="100%"><tr>
                        <td align="right"><el-dropdown trigger="click" @command="handleCommand" style="margin-right: 30px;">
                                <el-button type="warning" icon="el-icon-s-custom" circle></el-button>
                                <el-dropdown-menu slot="dropdown">
                                    <el-dropdown-item align="center" disabled>
                                        <hr>
                                    </el-dropdown-item>
                                    <el-dropdown-item align="center" disabled>{{ susr }}</el-dropdown-item>
                                    <el-dropdown-item align="center" disabled>
                                        <hr>
                                    </el-dropdown-item>
                                    <el-dropdown-item command="sloginOut" align="center">退出</el-dropdown-item>
                                </el-dropdown-menu>
                            </el-dropdown></td>
                        </tr>
                    </table>
                </td>
            </tr>
        </table>
    </div>
</template>

<script>
export default {
    name: 'CodeSuserTop',
    data() {
        return {
            susr: '',
        }
    },
    created() {
        this.isSLogin();
    },
    methods: {
        isSLogin() {
            let slogin = this.$cookies.checkSuserCookie();
            if (slogin) {
                this.susr = this.$cookies.getCookie('susername');
            } else {
                this.$router.push({ path: "/slogin" });
            }
        },
        sloginOut() {
            this.$cookies.deleteCookie('susername', 'spassword', -1);
            this.$router.push({ path: "/slogin" });
        },
        handleCommand(command) {
            if (command == "sloginOut") {
                this.sloginOut();
            }
        }
    }
}
</script>

<style>
.logo {
    width: 45px;
    height: 45px;
    float: left;
    margin-left: 30px;
    text-align: center;
}

.r2 {
    font-size: 20px;
    font-weight: bold;
    text-align: right;
    padding-right: 30px;
    border: 0;
}

.login_link {
    text-decoration: none;
    color: white;
}

#div1 {
    /* 顶栏 */
    width: 99.5%;
    height: 50px;
    min-width: 1280px;
    border-radius: 10px;
    margin-top: 5px;
    margin-left: 5px;
    margin-right: 5px;
    background-color: #1e1e1e;
    border: 1px solid #e8e8e8;
    transition: all .3s;
    box-shadow: 6px 6px 12px #c5c5c5,
        -6px -6px 12px #ffffff;
    border-collapse:collapse;
    float: left;
}
</style>