<template>
    <div id="div5">
        <table class="lr_table">
            <tr>
                <td class="left">
                    <img src="../assets/login.png" class="icon" />
                </td>
                <td class="right">
                    <table class="container">
                        <tr>
                            <td>
                                <el-form model="loginData">
                                    <table class="login">
                                        <tr>
                                            <td class="logintitle" colspan="2">code 社区登录</td>
                                        </tr>
                                        <tr>
                                            <td class="box">账号</td>
                                            <td><el-input type="text" size="small" placeholder="请输入用户名" name="username"
                                                    v-model="loginData.username"></el-input></td>
                                        </tr>
                                        <tr>
                                            <td class="box">密码</td>
                                            <td><el-input type="password" size="small" placeholder="请输入密码" name="password"
                                                    v-model="loginData.password"></el-input></td>
                                        </tr>
                                        <tr>
                                            <td colspan="2"><el-row>
                                                    <el-button type="primary" class="btnlogin" @click="checkLogin()"
                                                        round>登录</el-button></el-row>
                                            </td>
                                        </tr>
                                        <tr>
                                            <td align="left"><router-link to="/home" class="nonel">回首页</router-link></td>
                                            <td align="right"><router-link to="/register" class="noner">立即注册</router-link>
                                            </td>
                                        </tr>
                                        <tr>
                                            <td colspan="2" align="center"><router-link to="/slogin" class="nonel">管理员登录</router-link></td>
                                        </tr>
                                    </table>
                                </el-form>
                            </td>
                        </tr>
                    </table>
                </td>
            </tr>
        </table>
    </div>
</template>
 
<script>
import request from '../api/request'
export default ({

    name: 'CodeLogin',
    data() {
        return {
            loginData: {},//表单数据
        }
    },

    created(){
            this.isLogin();
        },
    
    methods: {
        isLogin(){
                let login = this.$cookies.checkCookie();
                if(login){
                    this.$router.push({ path: "/" });
                }
            },
        //登录检查
        checkLogin() {
            if (this.loginData.username != null && this.loginData.password != null) {
                //发送ajax请求
            return request({
                method: 'get',
                url:"/users/",
                    params: {
                        username: this.loginData.username,
                        password: this.loginData.password
                    }
                }).then((res) => {
                    // console.log(res.data);
                    if (res.data.code == 60011) {
                        this.$cookies.setCookie('username', this.loginData.username, 'password', this.loginData.password, 1);
                        this.$message.success(res.data.msg);
                        // 延迟1秒跳转
                        setTimeout(() => {
                            this.$router.push({ path: "/" })
                        }, 1000);
                    } else if (res.data.code == 60010) {
                        this.$message.error(res.data.msg);
                    } else {
                        this.$message.error(res.data.msg);
                    }
                });
            } else {
                alert("请输入用户名和密码！")
            }
        },
        //重置表单
        resetForm() {
            this.loginData = {};
        },
    }
});
</script>
  
<style>
.container {
    /* 登录注册框 */
    width: 90%;
    height: 90%;
    border: 1px solid blue;
    margin-left: 5%;
    margin-top: 5%;
    margin-right: 5%;
    margin-bottom: 5%;
    background-color: whitesmoke;
    border-radius: 30px;
    float: left;
}

.nonel {
    text-decoration: none;
    color: #909399;
    font-size: 13px;
}

.noner {
    text-decoration: none;
    color: #007acc;
    font-size: 13px;
}

.login {
    width: 60%;
    height: 50%;
    font-size: 15px;
    margin-left: 20%;
    text-align: center;
}

.box {
    width: 30%;
    /*两端对齐*/
    margin: auto;
    text-align: justify;
    text-align-last: justify;
    text-justify: inter-ideograph;
    /*兼容ie*/
}

.btnlogin {
    width: 100%;
}

.logintitle {
    font-size: 25px;
    font-weight: bold;
    text-align: center;
    color: #af66a6;
}

#div5 {
    width: 86%;
    height: 65%;
    min-width: 1280px;
    min-height: 550px;
    border: 2px solid white;
    background-color: white;
    border-radius: 20px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    float: left;
}

.left {
    width: 59%;
    height: 100%;
    float: left;
}

.right {
    width: 40%;
    height: 100%;
    float: left;
}

.icon {
    width: 100%;
    height: 100%;
    padding-left: 9px;
    border-radius: 20px;
}

.lr_table {
    width: 100%;
    height: 100%;
    text-align: center;
}</style>
  