<template>
  <div>
    <el-divider>编辑帖子</el-divider>
    <table class="tabletz">
      <tr>
        <td class="tzleft">帖子标题</td>
        <td class="tzright">
          <table class="tztitle">
            <tr>
              <td class="tzright"><el-input type="text" v-model="editArticle.editTitle" placeholder="请输入标题"></el-input>
              </td>
              <td class="tzleft">
                <CodeInsertLink />
              </td>
            </tr>
          </table>
        </td>
      </tr>
      <tr>
        <td class="tzleft">帖子内容</td>
        <td class="tzright">
          <div style="border: 1px solid #ccc;">
            <Toolbar style="border-bottom: 1px solid #ccc" :editor="editor" :defaultConfig="toolbarConfig" :mode="mode" />
            <Editor style="height: 450px; overflow-y: hidden;" v-model="html" :defaultConfig="editorConfig" :mode="mode"
              @onCreated="onCreated" />
          </div>
          <table class="btntable">
            <tr>
              <td align="left"><el-button type="primary" @click="saveEdit()">保存草稿</el-button></td>
              <td align="right"><el-button type="danger" @click="publishEdit()">立即发布</el-button></td>
            </tr>
          </table>
        </td>
      </tr>

    </table>

  </div>
</template>
  
<script>
import request from '../api/request'
import { ipurl } from '../api/ipurl.js'
import { Editor, Toolbar } from '@wangeditor/editor-for-vue'
import CodeInsertLink from './CodeInsertLink.vue'
export default ({
  name: 'CodeUserEditArticle',
  components: { Editor, Toolbar, CodeInsertLink },
  data() {
    return {
      maid: this.$route.params.maid,
      editArticle: {},
      editor: null,
      html: '',
      toolbarConfig: {
        excludeKeys: ["todo", "insertTable", "group-video", "bulletedList", "numberedList"]
      },
      editorConfig: {
        placeholder: '请输入内容...',
        MENU_CONF: {
          uploadImage: {
            fieldName: 'upimg',
            // base64LimitSize: 5 * 1024 * 1024 // 5M 以下插入 base64
            // 图片上传至minIO对象存储库
            server: ipurl + '/files/uploadimg',
          }
        }
      },
      mode: 'default', // or 'simple'
    }
  },
  mounted() {

    if (this.maid != null) {
      this.getArticleById();
    }
  },
  methods: {
    onCreated(editor) {
      this.editor = Object.seal(editor) // 一定要用 Object.seal() ，否则会报错
    },
    getArticleById() {
      return request({
        method: 'get',
        url: "/articles/getArticleById", params: { username: this.$cookies.getCookie("username"), id: this.maid }
      }).then((res) => {
        if (res.data.code == 60091) {
          this.editArticle = res.data.data;
          this.html = this.editArticle.editContent;
          console.log(this.editArticle);
        } else if (res.data.code == 60090) {
          this.$message.error(res.data.msg);
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },
    //保存检查
    saveEdit() {
      if (this.editArticle.editTitle != null && this.html != null) {
        this.editArticle.username = this.$cookies.getCookie("username");
        this.editArticle.status = "0";
        this.editArticle.editContent = this.html;
        //console.log(this.editArticle);
        //发送ajax请求
        if (this.maid != null) {
          this.editArticle.id = this.maid;
          return request({
            method: 'post',
            url: "/articles/updateArticleById", data: this.editArticle
          }).then((res) => {
            if (res.data.code == 60161) {
              this.$message.success(res.data.msg);
              this.resetForm();
              // 延迟1秒跳转
              setTimeout(() => {
                this.$router.push({ name: "temparticle" });
              }, 1000);
            } else if (res.data.code == 60160) {
              this.$message.error(res.data.msg);
            } else {
              this.$message.error(res.data.msg);
            }
          });
        } else {
          this.editArticle.editContent = this.html;
          return request({
            method: 'post',
            url: "/articles/save", data: this.editArticle
          }).then((res) => {
            if (res.data.code == 60081) {
              this.$message.success(res.data.msg);
              this.resetForm();
              // 延迟1秒跳转
              setTimeout(() => {
                this.$router.push({ name: "temparticle" });
              }, 1000);
            } else if (res.data.code == 60080) {
              this.$message.error(res.data.msg);
            } else {
              this.$message.error(res.data.msg);
            }
          });
        }

      } else {
        alert("标题和内容不能为空！")
      }
    },
    //发布检查
    publishEdit() {
      if (this.editArticle.editTitle != null && this.html != null) {
        this.editArticle.username = this.$cookies.getCookie("username");
        this.editArticle.status = "1";
        this.editArticle.editContent = this.html;
        //console.log(this.editArticle);
        //发送ajax请求
        if (this.maid != null) {
          this.editArticle.id = this.maid;
          return request({
            method: 'post',
            url: "/articles/updateArticleById", data: this.editArticle
          }).then((res) => {
            if (res.data.code == 60161) {
              this.$message.success(res.data.msg);
              this.resetForm();
              // 延迟1秒跳转
              setTimeout(() => {
                this.$router.push({ name: "upedarticle" });
              }, 1000);
            } else if (res.data.code == 60160) {
              this.$message.error(res.data.msg);
            } else {
              this.$message.error(res.data.msg);
            }
          });
        } else {
          this.editArticle.editContent = this.html;
          return request({
            method: 'post',
            url: "/articles/save", data: this.editArticle
          }).then((res) => {
            if (res.data.code == 60081) {
              this.$message.success(res.data.msg);
              this.resetForm();
              // 延迟1秒跳转
              setTimeout(() => {
                this.$router.push({ name: "upedarticle" });
              }, 1000);
            } else if (res.data.code == 60080) {
              this.$message.error(res.data.msg);
            } else {
              this.$message.error(res.data.msg);
            }
          });
        }
      } else {
        alert("标题和内容不能为空！")
      }
    },
    //重置表单
    resetForm() {
      this.editArticle = {};
      this.html = '';
    },
  },
  // mounted() {
  //   // 模拟 ajax 请求，异步渲染编辑器
  //   setTimeout(() => {
  //       this.html = '<p>模拟 Ajax 异步设置内容 HTML</p>'
  //   }, 500)
  // },
  beforeDestroy() {
    const editor = this.editor
    if (editor == null) return
    editor.destroy() // 组件销毁时，及时销毁编辑器
  },

})

</script>
  
<style src="@wangeditor/editor/dist/css/style.css"></style>
<style>
.tabletz{
  width: 99%;
  height: 100%;
}
.tzleft {
  width: 7.5%;
  text-align: center;
  font-weight: bold;
}

.tzright {
  width: 92.5%;
}

.btntable {
  width: 100%;
  margin-top: 10px;
}

.tztitle {
  width: 100%;
  margin: 0;
  padding: 0;
  border-collapse: collapse;
  float: left;
}
</style>
  