<template>
    <div>
        <table class="div1">
            <tr>
                <td width="33%"><el-link @click="home()" :underline="false"><img src="../assets/logo.png" class="logo" /></el-link></td>
                <td>
                    <!-- <input class="text" type="text" name="selectId" placeholder="请输入要查询的内容" required="required" />
						<button class="select" type="submit">搜索</button> -->
                    <el-input class="searchClass" v-model="selectinfo" v-if="search">
                        <el-button slot="append" icon="el-icon-search" @click="indexSelect()"></el-button>
                    </el-input>
                </td>
                <td align="right" width="33%">
                    <table width="100%" v-if="show">
                        <tr>
                            <td align="right"><el-dropdown trigger="click" @command="handleCommand">
                                    <!-- <el-button type="info" icon="el-icon-user-solid" circle></el-button> -->
                                    <el-avatar icon="el-icon-user-solid" :src="circleUrl"
                                        style="margin-top: 3px;"></el-avatar>
                                    <el-dropdown-menu slot="dropdown">
                                        <el-dropdown-item align="center" disabled>
                                            <hr>
                                        </el-dropdown-item>
                                        <el-dropdown-item align="center" disabled>{{ usr }}</el-dropdown-item>
                                        <el-dropdown-item align="center" disabled>
                                            <hr>
                                        </el-dropdown-item>
                                        <el-dropdown-item command="indexed" align="center">回首页</el-dropdown-item>
                                        <el-dropdown-item command="usercenter" align="center">个人中心</el-dropdown-item>
                                        <el-dropdown-item command="loginOut" align="center">退出</el-dropdown-item>
                                    </el-dropdown-menu>
                                </el-dropdown></td>
                            <td align="right"><el-button type="danger" @click="addArticle()" round
                                    style="margin-right: 30px;">创作</el-button></td>
                        </tr>
                    </table>
                    <table class="r2" v-if="!show">
                        <router-link class="login_link" to="/register">注册</router-link>
                        <font color="white"> &nbsp;|&nbsp; </font>
                        <router-link class="login_link" to="/login">登录</router-link>
                    </table>
                </td>
            </tr>
        </table>
    </div>
</template>

<script>
import {ipurl} from '../api/ipurl.js';
export default {
    name: 'CodeTop',
    data() {
        return {
            show: false,
            search: true,
            selectinfo: '',
            usr: '',
            circleUrl: ipurl + '/files/download?fileName=UserImg.jpg&bucket=' + this.$cookies.getCookie("username"),
        }
    },
    mounted() {
        this.isLogin();
        if (this.$router.currentRoute.path.includes("selectby")) {
                this.search = false;
            }
    },
    methods: {
        home(){
            this.search = true;
            sessionStorage.removeItem("selectInfo",'');
            this.$router.push({ path: "/home" });
        },
        indexSelect() {
            // this.$router.push({ name: "selectby", params: { sby: this.selectinfo } });
                // 跳转到新窗口
                let routeData = this.$router.resolve({ name: "selectby", params: { sby: this.selectinfo } });
                window.open(routeData.href, '_blank');

        },
        isLogin() {
            this.show = this.$cookies.checkCookie();
            if (this.show) {
                this.usr = this.$cookies.getCookie('username');
            }
        },
        loginOut() {
            this.$cookies.deleteCookie('username', 'password', -1);
            this.$router.push({ path: "/login" });
        },
        usercenter() {
            this.search = true;
            sessionStorage.removeItem("selectInfo",'');
            this.$router.push({ name: "userinfo" });
        },
        indexed() {
            this.search = true;
            sessionStorage.removeItem("selectInfo");
            this.$router.push({ path: "/home" });
        },
        addArticle() {
            this.$router.push({ name: "editarticle" });
        },
        handleCommand(command) {
            if (command == "usercenter") {
                this.usercenter();
            } else if (command == "loginOut") {
                this.loginOut();
            } else if (command == "indexed") {
                this.indexed();
            }
        }
    }
}
</script>

<style>
.logo {
    width: 45px;
    height: 45px;
    float: left;
    margin-left: 30px;
    text-align: center;
}

.r2 {
    font-size: 20px;
    font-weight: bold;
    text-align: right;
    padding-right: 30px;
    border: 0;
}

.login_link {
    text-decoration: none;
    color: white;
}

.div1 {
    /* 顶栏 */
    width: 99.5%;
    height: 50px;
    min-width: 1280px;
    border-radius: 10px;
    margin-top: 5px;
    margin-left: 5px;
    margin-right: 5px;
    background: #1e1e1e;
    border: 1px solid #e8e8e8;
    transition: all .3s;
    box-shadow: 6px 6px 12px #c5c5c5,
        -6px -6px 12px #ffffff;
    float: left;
}

.searchClass {
    border: 1px solid #c5c5c5;
    border-radius: 20px;
    background: #f4f4f4;
}

.searchClass .el-input-group__prepend {
    border: none;
    background-color: transparent;
    padding: 0 10px 0 30px;
}

.searchClass .el-input-group__append {
    border: none;
    background-color: transparent;
}

.searchClass .el-input__inner {
    height: 36px;
    line-height: 36px;
    border: none;
    background-color: transparent;
}

.searchClass .el-icon-search {
    font-size: 16px;
}

.searchClass .line {
    width: 1px;
    height: 26px;
    background-color: #c5c5c5;
    margin-left: 14px;
}

.searchClass:hover {
    border: 1px solid #D5E3E8;
    background: #fff;
}

.searchClass:hover .line {
    background-color: #D5E3E8;
}

.searchClass:hover .el-icon-search {
    color: #409eff;
    font-size: 16px;
}
</style>