<template>
  <div>
    <el-divider>个人信息</el-divider>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
      <table width="92%">
        <tr>
          <td>
            <el-form-item label="昵称" prop="name">
              <el-input v-model="ruleForm.name" style="width: 220px;"></el-input>
            </el-form-item>
            <el-form-item label="性别" prop="sex">
              <el-radio-group v-model="ruleForm.sex">
                <el-radio label="男"></el-radio>
                <el-radio label="女"></el-radio>
              </el-radio-group>
            </el-form-item>

            <el-form-item label="出生日期" prop="userdate">
              <el-date-picker type="date" placeholder="选择日期" v-model="ruleForm.userdate"
                style="width: 220px;"></el-date-picker>
            </el-form-item>
          </td>
          <td align="right">
            <table>
              <tr>
                <td align="center">
                  <CodeUserImgUpload/>
                </td>
              </tr>
              <!-- <tr>
                <td class="headerimg">
                  上传头像
                </td>
              </tr> -->
            </table>
            
          </td>
        </tr>
      </table>
      <el-form-item label="个性标签">
        <el-select v-model="ruleForm.tag" placeholder="请选择个性标签">
          <el-option label="请选择" value="请选择"></el-option>
          <el-option label="Java" value="Java"></el-option>
          <el-option label="python" value="python"></el-option>
          <el-option label="C/C++" value="C/C++"></el-option>
          <el-option label="Go" value="Go"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="个人简介" prop="brief">
        <el-input type="textarea" v-model="ruleForm.brief" style="width: 92%;" :autosize="{ minRows: 5 }"></el-input>
      </el-form-item>

      <el-form-item>
        <el-button type="primary" @click="setUserInfo()">保存</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
  
<script>
import request from '../api/request'
import CodeUserImgUpload from './CodeUserImgUpload.vue'
export default ({
  name: 'CodeUserInfo',
  components: {
    CodeUserImgUpload,
  },
  data() {
    return {
      ruleForm: {
        name: '',
        userdate: '',
        tag: '',
        sex: '',
        brief: '',

        dialogImageUrl: '',
        dialogVisible: false,
        disabled: false,
      },
      rules: {
        // name: [
        //   { required: false, message: '请输入活动名称', trigger: 'blur' },
        //   { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        // ],

        // date: [
        //   { type: 'date', required: false, message: '请选择日期', trigger: 'change' }
        // ],

        // type: [
        //   { type: 'array', required: false, message: '请至少选择一个个性标签', trigger: 'change' }
        // ],
        // sex: [
        //   { required: false, message: '性别', trigger: 'change' }
        // ],
        // brief: [
        //   { required: false, message: '请填写个人简介', trigger: 'blur' }
        // ]
      }
    };
  },
  //钩子函数，VUE对象初始化完成后自动执行
  created() {
    this.getUserInfo();
  },
  methods: {
    //列表
    getUserInfo() {
      //发送ajax请求
      return request({
        method: 'get',
        url: "/users/getInfo", params: { username: this.$cookies.getCookie("username") }
      }).then((res) => {
        this.ruleForm = res.data.data;
        console.log(res.data);
      });
    },
    //列表
    setUserInfo() {
      this.ruleForm.username = this.$cookies.getCookie("username");
      console.log(this.ruleForm);
      //发送ajax请求
      return request({
        method: 'post',
        url: "/users/setInfo", data: this.ruleForm
      }).then((res) => {
        if (res.data.code == 60111) {
          this.$message.success(res.data.msg);
        } else if (res.data.code == 60110) {
          this.$message.error(res.data.msg);
        } else {
          this.$message.error(res.data.msg);
        }
      });
    },

    handleRemove(file) {
        console.log(file);
      },
      handlePictureCardPreview(file) {
        this.dialogImageUrl = file.url;
        this.dialogVisible = true;
      },
      handleDownload(file) {
        console.log(file);
      },
  }
})

</script>
  
<style>
.headerimg{
  text-align: center;
  font-size: 12px;
  color: #606266;
}
</style>
  