<template>
  <div id="aleft">
    <div class="ucard">
      <table width="100%" height="100%">
        <tr>
          <td colspan="2" align="center">
            <el-link @click="toUserIndex()" :underline="false" style="margin-top: 30px;">
              <el-avatar icon="el-icon-user-solid" :size="size" :src="ImgUrl"></el-avatar></el-link>
          </td>
        </tr>
        <tr>
          <td colspan="2" align="center"><i class="iname"  @click="toUserIndex()">{{ ruleForm.name }}</i></td>
        </tr>
        <tr>
          <td colspan="2" align="center"><el-button type="success" size="mini" plain round disabled>{{ ruleForm.tag }}</el-button></td>
        </tr>
        <tr>
          <td colspan="2" align="center"><i class="ibrief">{{ ruleForm.brief }}</i></td>
        </tr>
        <tr>
          <td align="center">
            <el-button size="mini" v-if="!btnShow" style="margin-bottom: 25px;" round @click="isattention()">关注</el-button>
            <el-button size="mini" v-if="btnShow" style="margin-bottom: 25px;" round @click="isattention()">已关注</el-button></td>
          <td align="center"><el-button size="mini" style="margin-bottom: 25px;" round>私信</el-button></td>
        </tr>
      </table>
    </div>
    <div style="height: 15px;"></div>
    <div class="acard">
      <table width="100%" height="100%">
        <tr>
          <td class="bzwz">博主最新文章</td>
        </tr>
        <tr>
          <td>
            <div v-for="articles in showArticles" :key="articles" class="articles">
              <el-link :underline="false" @click="toArticleShow(articles)">{{ articles.editTitle }}</el-link>
              &nbsp;&nbsp;<i class="el-icon-view"></i>&nbsp;{{ articles.reading }}&nbsp;
              <hr color="white">
            </div>
          </td>
        </tr>
      </table>
    </div>
  </div>
</template>
  
<script>
import request from '../api/request'
import { ipurl } from '../api/ipurl'
export default ({
  name: 'CodeArticleLeft',
  components: {
  },
  data() {
    return {
      ruleForm: {},
      showArticles: {},
      ImgUrl: '',
      btnShow: false,
      size: 80


    }
  },
  mounted() {
    setTimeout(() => {
      this.getUserInfo();
      this.ImgUrl = ipurl + '/files/download?fileName=UserImg.jpg&bucket=' + sessionStorage.getItem("ausername");
      setTimeout(() => {
      this.getAllFiles1(); }, 200);
    }, 100);
  },
  methods: {
    toUserIndex() {
      this.$message.success(sessionStorage.getItem("ausername"));
    },
    isattention() {
      this.btnShow = !this.btnShow;
    },
    toArticleShow(articles) {
      // 跳转到新窗口
      let routeData = this.$router.resolve({ name: 'articleshow', params: { aid: articles.id } });
      window.open(routeData.href, '_blank');
    },
    //列表
    getAllFiles1() {
      //发送ajax请求
      return request({
        method: 'get',
        url: "/articles/selectArticleTitle", params: { username: sessionStorage.getItem("ausername"), status: "1", limits: 7 }
      }).then((res) => {
        this.showArticles = res.data.data;
      });
    },
    //列表
    getUserInfo() {
      //发送ajax请求
      return request({
        method: 'get',
        url: "/users/getInfo", params: { username: sessionStorage.getItem("ausername") }
      }).then((res) => {
        this.ruleForm = res.data.data;
      });
    },
  }
})

</script>
  
<style>
#aleft {
  width: 320px;
  height: 100%;
  margin-top: 20px;
  margin-right: 20px;
  border-radius: 5px;
  float: right;
}

.ucard {
  width: 320px;
  border-radius: 10px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1)
}

.acard {
  width: 320px;
  border-radius: 10px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1)
}

.ibrief {
  font-size: 12px;
  color: #9093a5;
}

.iname {
  font-size: 15px;
  font-weight: bold;
  cursor: pointer;
}

.bzwz {
  width: 100%;
  font-size: 15px;
  padding-top: 10px;
  font-weight: bold;
  text-align: center;
}

.articles {
  text-align: left;
  font-size: 14px;
  margin: 5px;
}</style>
  