<template>
    <div id="div5">
        <table class="lr_table">
            <tr>
                <td class="left">
                    <img src="../assets/login.png" class="icon" />
                </td>
                <td class="right">
                    <table id="container">
                        <tr>
                            <td>
                                <el-form model="registerData">
                                    <table class="register">
                                        <tr>
                                            <td class="registertitle" colspan="2">code 社区注册</td>
                                        </tr>
                                        <tr>
                                            <td class="box">用户名</td>
                                            <td><el-input type="text" size="small" placeholder="请输入用户名" name="username"
                                                    v-model="registerData.username"></el-input></td>
                                        </tr>
                                        <tr>
                                            <td class="box">密码</td>
                                            <td><el-input type="password" size="small" placeholder="请输入密码" name="password"
                                                    v-model="registerData.password"></el-input></td>
                                        </tr>
                                        <tr>
                                            <td class="box">确认密码</td>
                                            <td><el-input type="password" size="small" placeholder="请再次输入密码"
                                                    name="password2" v-model="registerData.password2"></el-input></td>
                                        </tr>
                                        <tr>
                                            <td class="box">邮箱</td>
                                            <td><el-input type="text" size="small" placeholder="请输入邮箱" name="email"
                                                    v-model="registerData.email"></el-input></td>
                                        </tr>
                                        <tr>
                                            <td class="box">验证码</td>
                                            <td>
                                                <el-input type="text" size="small" placeholder="请输入验证码" name="pwd"
                                                    v-model="registerData.pwd" style="width: 60%" class="boxr"
                                                    clearable></el-input>
                                                <el-button type="info" size="small" style="width: 40%" class="boxr"
                                                    icon="el-icon-message" @click="checkEmail()" v-if="!show">发送</el-button>
                                                <el-button type="info" size="small" style="width: 40%" class="boxr"
                                                    icon="el-icon-message" v-if="show" disabled>{{ this.count
                                                    }}秒</el-button>
                                            </td>
                                        </tr>
                                        <tr>
                                            <td colspan="2"><el-row>
                                                    <el-button type="primary" class="btnlogin" @click="checkRegister()"
                                                        round>注册</el-button></el-row></td>
                                        </tr>
                                        <tr>
                                            <td align="left"><router-link to="/home" class="nonel">回首页</router-link></td>
                                            <td colspan="2" align="right"><router-link class="noner"
                                                    to="/login">立即登录</router-link></td>
                                        </tr>
                                    </table>
                                </el-form>
                            </td>
                        </tr>
                    </table>
                </td>
            </tr>
        </table>
    </div>
</template>
  
<script>
import request from '../api/request'
export default ({

    name: 'CodeRegister',
    data() {
        return {
            registerData: {},//表单数据
            receiveData: [],//验证码
            show: false,
            count: 0,
            // rules: {//校验规则
            //     username: [{ required: true, message: '请输入用户名', trigger: 'blur' }],
            //     password: [{ required: true, message: '请输入密码', trigger: 'blur' }],
            //     password2: [{ required: true, message: '请输入密码', trigger: 'blur' }],
            //     pwd: [{ required: true, message: '请输入验证码', trigger: 'blur' }],
            //     email: [{ required: true, message: '请输入邮箱', trigger: 'blur' }]
            // }
        }
    },

    methods: {
        //注册检查
        checkRegister() {
            if (this.registerData.pwd == this.receiveData && this.registerData.pwd != null) {
                //发送ajax请求
                this.registerData.name = this.registerData.username;
                this.registerData.sex = "男";
                this.registerData.userdate = "2023-01-01";
                this.registerData.tag = "请选择";
                this.registerData.brief = "这个人很懒，什么都没有写！";
                return request({
                    method: 'post',
                    url: "/users/",
                    data: this.registerData
                }).then((res) => {
                    //console.log(res.data);
                    if (res.data.code == 60021) {
                        this.$message.success(res.data.msg);
                        //清除cookie
                        this.$cookies.deleteCookie('username', 'password', -1);
                        // 延迟1秒跳转
                        setTimeout(() => {
                            this.$router.push({ path: "/login" })
                        }, 1000);
                    } else if (res.data.code == 60020) {
                        this.$message.error(res.data.msg);
                    } else {
                        this.$message.error(res.data.msg);
                    }
                });
            } else {
                alert("验证码错误！")
            }
        },
        //重置表单
        resetForm() {
            this.registerData = {};
        },
        timeCount() {
            this.count = 60;
            this.show = true;
            this.timer = setInterval(() => {
                if (this.count > 0 && this.count <= 60) {
                    this.count--;
                    console.log("倒计时中。。。");
                } else {
                    console.log("倒计时结束！");
                    this.show = false;
                    clearInterval(this.timer);
                    this.timer = null;
                }
            }, 1000);
        },

        checkEmail() {
            if (this.registerData.username != null && this.registerData.password != null && this.registerData.email != null) {
                if (this.registerData.password == this.registerData.password2) {
                    //发送ajax请求
                    return request({
                        method: 'get',
                        url: "/users/checkuser", params: { username: this.registerData.username }
                    }).then((res) => {
                        console.log(res.data);
                        if (res.data.code == 60001) {
                            //发送ajax请求
                            return request({
                                method: 'get',
                                url: "/users/checkemail", params: { email: this.registerData.email, label: "register" }
                            }).then((res) => {
                                //console.log(res.data);
                                if (res.data.code == 60071) {
                                    this.receiveData = res.data.data;
                                    this.$message.success(res.data.msg);
                                    this.timeCount();
                                } else if (res.data.code == 60070) {
                                    this.$message.error(res.data.msg);
                                } else {
                                    this.$message.error(res.data.msg);
                                }
                            });
                        } else if (res.data.code == 60000) {
                            this.$message.error(res.data.msg);
                        } else {
                            this.$message.error(res.data.msg);
                        }
                    });
                } else {
                    alert("两次密码不一致！")
                }
            } else {
                alert("请输入！")
            }
        }
    }
});
</script>
  
<style>
#container {
    /* 登录注册框 */
    width: 90%;
    height: 90%;
    border: 1px solid blue;
    margin-left: 5%;
    margin-top: 5%;
    margin-right: 5%;
    margin-bottom: 5%;
    background-color: whitesmoke;
    border-radius: 30px;
    float: left;
}

.noner {
    text-decoration: none;
    color: #007acc;
    font-size: 13px;
}

.register {
    width: 60%;
    height: 60%;
    font-size: 15px;
    margin-left: 20%;
    text-align: center;
}

.box {
    width: 30%;
    /*两端对齐*/
    margin: auto;
    text-align: justify;
    text-align-last: justify;
    text-justify: inter-ideograph;
    /*兼容ie*/
}

.btnlogin {
    width: 100%;
}

.registertitle {
    font-size: 25px;
    font-weight: bold;
    text-align: center;
    color: #af66a6;
}

#div5 {
    width: 86%;
    height: 65%;
    min-width: 1280px;
    min-height: 550px;
    border: 2px solid white;
    background-color: white;
    border-radius: 20px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    float: left;
}

.left {
    width: 59%;
    height: 100%;
    float: left;
}

.right {
    width: 40%;
    height: 100%;
    float: left;
}

.icon {
    width: 100%;
    height: 100%;
    padding-left: 9px;
    border-radius: 20px;
}

.lr_table {
    width: 100%;
    height: 100%;
    text-align: center;
}
</style>
  