<template>
    <div>
        <el-divider>编写公告</el-divider>
        <el-form :model="noticeForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
            <el-form-item label="公告标题" prop="noticeTitle">
                <el-input v-model="noticeForm.noticeTitle" placeholder="请输入公告标题" style="width: 1070px;"></el-input>
            </el-form-item>
            <el-form-item label="公告内容" prop="noticeContent">
                <el-input type="textarea" placeholder="请输入公告内容" style="width: 1070px;" :autosize="{ minRows: 22 }"
                    v-model="noticeForm.noticeContent"></el-input>
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="saveNotice()">暂存</el-button>

                <el-button type="danger" style="float: right; margin-right: 100px;" @click="publishNotice()">发布</el-button>
            </el-form-item>
        </el-form>
    </div>
</template>
  
<script>
import request from '../api/request'
export default ({
    name: 'CodeSuserCreateNotice',
    components: {
    },
    data() {
        return {
            noticeForm: {},
            nid: this.$route.params.nid,
            rules: {
                // name: [
                //   { required: false, message: '请输入活动名称', trigger: 'blur' },
                //   { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
                // ],

                // date: [
                //   { type: 'date', required: false, message: '请选择日期', trigger: 'change' }
                // ],

                // type: [
                //   { type: 'array', required: false, message: '请至少选择一个个性标签', trigger: 'change' }
                // ],
                // sex: [
                //   { required: false, message: '性别', trigger: 'change' }
                // ],
                // brief: [
                //   { required: false, message: '请填写个人简介', trigger: 'blur' }
                // ]
            }
        };
    },
    mounted() {
        if(this.nid!=null){
            this.getNoticeById();
        }
    },
    methods: {
        //重置表单
        resetForm() {
            this.noticeForm = {};
        },
        getNoticeById() {
            //发送ajax请求
            return request({
                method: 'get',
                url:"/susers/notices/selectNoticeById",
                params: { susername: this.$cookies.getCookie("susername"), id: this.nid } }).then((res) => {
                        if (res.data.code == 60291) {
                            this.noticeForm = res.data.data;
                        } else {
                            this.$message.error(res.data.msg);
                        }   
                    });
        },
        //列表
        saveNotice() {
            //发送ajax请求
            this.noticeForm.susername = this.$cookies.getCookie("susername");
            this.noticeForm.status = '0'
            console.log(this.noticeForm);
            if(this.nid!=null){
                return request({
                method: 'post',
                url:"/susers/notices/updateNoticeById", data:this.noticeForm}).then((res) => {
                    if(res.data.code == 60321){
                        this.$message.success(res.data.msg);
                        this.resetForm();
                        // 延迟1秒跳转
                        setTimeout(() => {
                        this.$router.push({ name: "unnoticed" });
                    }, 1000);
                    } else if (res.data.code == 60320) {
                        this.$message.error(res.data.msg);
                    } else {
                        this.$message.error(res.data.msg);
                    }
                });
            } else {
            //发送ajax请求
            return request({
                method: 'post',
                url:"/susers/notices/save", data:this.noticeForm}).then((res) => {
                if (res.data.code == 60281) {
                    this.$message.success("保存成功！");
                    this.resetForm();
                    // 延迟1秒跳转
                    setTimeout(() => {
                        this.$router.push({ name: "unnoticed" });
                    }, 1000);
                } else if (res.data.code == 60280) {
                    this.$message.error("保存失败！");
                } else {
                    this.$message.error(res.data.msg);
                }
            });
        }
        },
        //列表
        publishNotice() {
            this.noticeForm.susername = this.$cookies.getCookie("susername");
            this.noticeForm.status = '1'
            console.log(this.noticeForm);
            if(this.nid!=null){
                return request({
                method: 'post',
                url:"/susers/notices/updateNoticeById", data:this.noticeForm}).then((res) => {
                    if(res.data.code == 60321){
                        this.$message.success(res.data.msg);
                        this.resetForm();
                        // 延迟1秒跳转
                        setTimeout(() => {
                        this.$router.push({ name: "noticed" });
                    }, 1000);
                    } else if (res.data.code == 60320) {
                        this.$message.error(res.data.msg);
                    } else {
                        this.$message.error(res.data.msg);
                    }
                });
            } else {
            //发送ajax请求
            return request({
                method: 'post',
                url:"/susers/notices/save", data:this.noticeForm}).then((res) => {
                if (res.data.code == 60281) {
                    this.$message.success("发布成功！");
                    this.resetForm();
                    // 延迟1秒跳转
                    setTimeout(() => {
                        this.$router.push({ name: "noticed" });
                    }, 1000);
                } else if (res.data.code == 60280) {
                    this.$message.error("发布失败！");
                } else {
                    this.$message.error(res.data.msg);
                }
            });
        }
        }
    }
})

</script>
  
<style></style>
  