<template>
    <div>
        <el-button type="info" @click="show()">插入链接</el-button>

        <el-dialog title="链接中心" :visible.sync="dialogTableVisible" width="60%">
            <el-tabs v-model="activeName">
                <el-tab-pane label="我的帖子" name="myarticle">
                    <el-table tooltip-effect="dark" max-height="350"
                        :data="articleList1.slice((currentPage1 - 1) * pageSize1, currentPage1 * pageSize1)"
                        :default-sort="{ prop: 'createTime', order: 'descending' }">
                        <el-table-column type="index" label="序号" width="55" align="center"></el-table-column>
                        <el-table-column prop="id" label="帖子ID" width="95" align="center">
                        </el-table-column>
                        <el-table-column prop="editTitle" label="标题" align="left" :show-overflow-tooltip="true">
                        </el-table-column>
                        <el-table-column prop="editContenthtml" label="内容" align="left" :show-overflow-tooltip="true">
                        </el-table-column>
                        <el-table-column prop="createTime" label="发布日期" width="180" align="left">
                        </el-table-column>
                        <el-table-column label="操作" width="170" align="left">
                            <template slot-scope="scope">
                                <el-button size="mini" type="success" @click="handleArticleCopy(scope.row)"
                                    round>复制选中帖子链接</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    <el-pagination background layout="prev, pager, next,jumper, ->, total, slot"
                        :total="articleList1.length" @current-change="handleCurrentChange1" :current-page="currentPage1"
                        :page-size="pageSize1" style="text-align: right">
                    </el-pagination>
                </el-tab-pane>
                <el-tab-pane label="代码仓库" name="codebase">  
                    <el-table ref="multipleTable" tooltip-effect="dark" max-height="350"
                        :data="fileList"
                        @selection-change="handleSelectionChange" :default-sort="{ prop: 'objectName' }">
                        <el-table-column type="selection" width="55">
                        </el-table-column>
                        <el-table-column type="index" label="序号" width="55" align="center"></el-table-column>
                        <el-table-column prop="objectName" label="文件名" align="left">
                        </el-table-column>
                        <el-table-column prop="size" label="大小(Byte)" width="250" align="left">
                        </el-table-column>
                        <el-table-column label="操作" width="150" align="left">
                            <template slot-scope="scope">
                                <el-button size="mini" type="success" @click="handleCopy(scope.row)">复制下载链接</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                    <el-button size="mini" type="primary" @click="handleCopys()" style="float: left;margin-top: 2px;">复制选中文件链接</el-button>
                    <!-- <el-pagination background layout="prev, pager, next,jumper, ->, total, slot" :total="fileList.length"
                        @current-change="handleCurrentChange2" :current-page="currentPage2" :page-size="pageSize2"
                        style="text-align: right">
                    </el-pagination> -->
                </el-tab-pane>
            </el-tabs>
        </el-dialog>
    </div>
</template>
   
<script>
import request from '../api/request'
import { ipurl, articleurl } from '../api/ipurl'
export default {
    name: 'CodeInsertLink',
    data() {
        return {
            link: '',
            activeName: 'myarticle',
            articleList1: [],
            currentPage1: 1, 
            pageSize1: 5,
            // currentPage2: 1, 
            // pageSize2: 5, 
            dialogTableVisible: false,
            fileList: [],
            Names: [],
            bucketName: { buckets: this.$cookies.getCookie("username") },
            multipleSelection: []
        };
    },
    methods: {
        show() {
            if (this.fileList.length == 0) {
                this.getAllFiles();
            }
            if (this.articleList1.length == 0) {
                this.getAllArticles1();
            }
            this.dialogTableVisible = true;
        },
        //点击按钮切换页面
        handleCurrentChange1(currentPage1) {
            this.currentPage1 = currentPage1;
        },
        // handleCurrentChange2(currentPage2) {
        //     this.currentPage2 = currentPage2;
        // },
        getAllArticles1() {
            //发送ajax请求
            return request({
                method: 'get',
                url: "/articles/selectByUsername", params: { username: this.$cookies.getCookie("username"), status: "1" }
            }).then((res) => {
                this.articleList1 = res.data.data;
                for (var i = 0; i < this.articleList1.length; i++) {
                    this.articleList1[i].editContenthtml = this.articleList1[i].editContent.replace(/<[^>]+>/g, "");
                }
            });
        },
        handleArticleCopy(row) {
            this.link = articleurl + row.id;
            this.$copyText(this.link).then(
                e => {
                    this.$message.success("ID为【" + row.id + '】的帖子访问链接复制成功!');
                    console.log('复制成功：', e);
                },
                e => {
                    this.$message.error('复制失败!');
                    console.log('复制失败：', e);
                }
            )
        },
        //列表
        getAllFiles() {
            //发送ajax请求
            return request({
                method: 'get',
                url: "/files/filelist", params: { bucket: this.$cookies.getCookie("username") }
            }).then((res) => {
                this.fileList = res.data.data;
            });
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
            let Names = [];
            let files = val;
            files.forEach(item => {
                Names.push(item.objectName)
            })
            this.Names = Names;
        },
        handleCopy(row) {
            this.link = ipurl + '/files/download?fileName=' + row.objectName + "&bucket=" + this.$cookies.getCookie("username");
            this.$copyText(this.link).then(
                e => {
                    this.$message.success("【" + row.objectName + '】文件下载链接复制成功!');
                    console.log('复制成功：', e);
                },
                e => {
                    this.$message.error('复制失败!');
                    console.log('复制失败：', e);
                }
            )
        },
        handleCopys() {
            this.link = ipurl + '/files/downloads?fileNames=' + this.Names + "&bucket=" + this.$cookies.getCookie("username");
            this.$copyText(this.link).then(
                e => {
                    this.$message.success('文件下载链接复制成功!');
                    console.log('复制成功：', e);
                },
                e => {
                    this.$message.error('复制失败!');
                    console.log('复制失败：', e);
                }
            )
        },
    }
};
</script>
  