<template>
    <div>
        <table>
            <tr>
                <td align="center">
                    <table>
                        <tr>
                            <td align="center"><el-image style="width: 150px; height: 150px" :src="circleUrl" fit="contain">
                                    <div slot="error" class="image-slot">
                                        <img src="../assets/ulogo.png" class="ulogo" />
                                    </div>
                                </el-image></td>
                        </tr>
                        <tr>
                            <td class="headerimg">当前头像</td>
                        </tr>
                    </table>
                </td>
                <td>
                    <table>
                        <tr>
                            <td>
                                <el-upload :action="uploadURL" list-type="picture-card" :on-change="handleChange"
                                    ref="upload" :auto-upload="false" :limit="1" name="upfiles" :data="UserImg"
                                    :on-success="handleUploads1" :on-error="handleUploads2">
                                    <i slot="default" class="el-icon-plus"></i>
                                    <div slot="file" slot-scope="{file}">
                                        <img class="el-upload-list__item-thumbnail" :src="file.url" alt="">
                                        <span class="el-upload-list__item-actions">
                                            <span class="el-upload-list__item-preview"
                                                @click="handlePictureCardPreview(file)">
                                                <i class="el-icon-zoom-in"></i>
                                            </span>
                                        </span>
                                    </div>
                                </el-upload>
                                <el-dialog :visible.sync="dialogVisible">
                                    <img width="100%" :src="dialogImageUrl" alt="">
                                </el-dialog>
                            </td>
                        </tr>
                        <tr>
                            <td class="headerimg">点击上传新头像</td>
                        </tr>
                    </table>
                </td>
            </tr>
        </table>
    </div>
</template>

<script>
import {ipurl} from '../api/ipurl.js'
export default {
    data() {
        return {
            uploadURL: ipurl + '/files/uploads',
            circleUrl: ipurl + '/files/download?fileName=UserImg.jpg&bucket=' + this.$cookies.getCookie("username"),
            dialogImageUrl: '',
            dialogVisible: false,
            disabled: false,
            UserImg: { buckets: this.$cookies.getCookie("username") },
        };
    },
    methods: {
        handleChange(file) {
            let fileName = 'UserImg.jpg'  // 如果可以上传多个文件，这里需要用fileList.forEach()处理
            let f = new File([file.raw], fileName);
            f.uid = file.uid; // new File 没有uid属性，会导致组件底层报错，这里手动加上
            file.raw = f;  // 用f替换file的数据
            // console.log(file.raw)
            this.$refs.upload.submit(); // 手动触发上传
            setTimeout(() => {
                // 刷新页面
                this.$router.go(0);
            }, 1000);
        },

        handleUploads1() {
            this.$message.success("上传成功！");
        },
        handleUploads2() {
            this.$message.error("上传失败！");
        },
        // handleRemove(file) {
        //     console.log(file);
        // },
        handlePictureCardPreview(file) {
            this.dialogImageUrl = file.url;
            // console.log(file.url);
            this.dialogVisible = true;
        },
        // handleDownload(file) {
        //     console.log(file);
        // }
    }
}
</script>
<style>
.ulogo{
    width: 50%;
    height: 55%;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
}
</style>