<template>
    <div>
        <div class="collectionblocks">
            <el-timeline>
                <el-timeline-item
                    v-for="collection in collectionForm.slice((currentPage - 1) * pageSize, currentPage * pageSize)"
                    :key="collection">
                    <table class="alist_collections">
                        <tr>
                            <td class="alist_collection">
                                <table width="100%" height="100%">
                                    <tr>
                                        <td class="titles"><el-link :underline="false"
                                                style="font-size: 18px;font-weight: bold;"
                                                @click="toArticleShow(collection)">{{ collection.editTitle }}</el-link></td>
                                    </tr>
                                    <tr>
                                        <td class="contents_history">{{ collection.editContenthtml }}</td>
                                    </tr>
                                </table>
                            </td>
                            <td class="starRight">
                                <img class="star" src="../assets/staron.png" @click="noStar(collection)" />
                            </td>
                        </tr>
                    </table>
                </el-timeline-item>
            </el-timeline>
        </div>
        <el-pagination background layout="prev, pager, next,jumper, ->, total, slot" :total="collectionForm.length"
            @current-change="handleCurrentChange" :current-page="currentPage" :page-size="pageSize"
            style="text-align: right;margin-right: 30px;">
        </el-pagination>
    </div>
</template>
    
<script>
import request from '../api/request'
export default ({
    name: 'CodeUserCollectionResoult',
    components: {
    },
    data() {
        return {
            collectionForm: {},
            gcollections: {
                username: this.$cookies.getCookie("username"),
                favorites: this.$route.params.fname,
            },
            currentPage: 1, //当前页 刷新后默认显示第一页
            pageSize: 6, //每一页显示的数据量 此处每页显示6条数据
            dCollections: {
                articleId: '',
                username: this.$cookies.getCookie("username"),
                favorites: '默认收藏夹'
            },
        };
    },
    mounted() {
        this.getCollections();
    },
    methods: {
        noStar(collection) {
            this.$confirm('此操作将取消对【' + collection.editTitle + '】的收藏, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.dCollections.articleId = collection.articleId;
                return request({
                    method: 'post',
                    url: "/users/deleteCollections",
                    data: this.dCollections
                }).then((res) => {
                    if (res.data.code == 60381) {
                        this.$message.info("取消收藏成功！");
                        this.getCollections();
                    } else if (res.data.code == 60380) {
                        this.$message.error(res.data.msg);
                    } else {
                        this.$message.error(res.data.msg);
                    }
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消'
                });
            });
        },
        //点击按钮切换页面
        handleCurrentChange(currentPage) {
            this.currentPage = currentPage; //每次点击分页按钮，当前页发生变化
            // console.log(this.currentPage);
        },
        toArticleShow(collection) {
            // 跳转到新窗口
            let routeData = this.$router.resolve({ name: 'articleshow', params: { aid: collection.articleId } });
            window.open(routeData.href, '_blank');
        },
        //列表
        getCollections() {
            //发送ajax请求
            return request({
                method: 'get',
                url: "/users/getCollections", params: { username: this.gcollections.username, favorites: this.gcollections.favorites }
            }).then((res) => {
                this.collectionForm = res.data.data;
                for (var i = 0; i < this.collectionForm.length; i++) {
                    this.collectionForm[i].editContenthtml = this.collectionForm[i].editContent.replace(/<[^>]+>/g, "");
                }
                console.log(this.collectionForm);
            });
        },
    }
})
</script>
    
<style>
.collectionblocks {
    width: 86%;
    margin-left: 4%;
    margin-top: 10px;
}

.alist_collections {
    width: 100%;
    border-radius: 5px;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}

.alist_collection {
    width: 90%;
    text-align: left;
}

.starRight {
    width: 10%;
    text-align: center;
}
</style>
    