function checkCookie() {
    let username = getCookie("username");
    let password = getCookie("password");
    if (username != null && password != null) {
        console.log("已登录！");
        return true;
    } else {
        console.log("未登录！");
        return false;
    }
}
function checkSuserCookie() {
    let susername = getCookie("susername");
    let spassword = getCookie("spassword");
    if (susername != null && spassword != null) {
        console.log("管理员已登录！");
        return true;
    } else {
        console.log("管理员未登录！");
        return false;
    }
}
function getCookie(key) {
    if (document.cookie.length > 0) {
        let arr = document.cookie.split("; ").map(cookie => cookie.split('='));
        //console.log(arr);
        let cookie = {};
        for (let i = 0; i < arr.length; i++) {
            let name = arr[i][0];
            let value = arr[i][1];
            cookie[name] = decodeURIComponent(value);
        }
        return cookie[key];
    }
}
function deleteCookie(c_username, c_password, expiredays) {
    setCookie(c_username, null, c_password, null, expiredays);
}
function setCookie(c_username, value1,c_password, value2, expiredays) {
    var exdate = new Date();
    exdate.setDate(exdate.getDate() + expiredays);
    let key1 = c_username;
    let key2 = c_password;
    let value01 = encodeURIComponent(value1);
    let value02 = encodeURIComponent(value2);
    let times = expiredays*60*60*24;
    document.cookie = `${key1}=${value01}; max-age=${times};`;
    document.cookie = `${key2}=${value02}; max-age=${times};`;
    //console.log(document.cookie);
}
function setArticleCookie(article_id, value1, expiredays) {
    var exdate = new Date();
    exdate.setDate(exdate.getDate() + expiredays);
    let key1 = article_id;
    let value01 = encodeURIComponent(value1);
    let times = expiredays;
    document.cookie = `${key1}=${value01}; max-age=${times};`;
    //console.log(document.cookie);
}
module.exports = {
    setCookie,
    getCookie,
    deleteCookie,
    checkSuserCookie,
    checkCookie,
    setArticleCookie,
  }