<template>
    <div>
        <el-divider>代码仓库</el-divider>
        <el-upload class="upload-demo" :action=uploadURL name="upfiles" :data="bucketName" auto-upload
            :show-file-list="true" multiple :on-success="handleUploads1" :on-error="handleUploads2">
            <el-button type="primary" style="margin-left: 30px;">点击上传</el-button>
            <el-link type="info" disabled>&nbsp;<i class="el-icon-upload"></i>上传文件不超过100MB，可批量上传</el-link>
        </el-upload>
        <hr>
        <el-button type="success" @click="handleDownloads()" style="margin-left: 30px;">批量下载</el-button>&nbsp;
        <el-button type="danger" @click="handleDeletes()">批量删除</el-button>
        <hr>
        <el-table ref="multipleTable" tooltip-effect="dark" max-height="475"
            :data="fileList"
            @selection-change="handleSelectionChange" :default-sort="{ prop: 'objectName' }">
            <el-table-column type="selection" width="70" align="center">
            </el-table-column>
            <el-table-column type="index" label="序号" width="55" align="center"></el-table-column>
            <el-table-column prop="objectName" label="文件名" align="left">
            </el-table-column>
            <el-table-column prop="size" label="大小(Byte)" width="250" align="left">
            </el-table-column>
            <el-table-column label="操作" width="200" align="left">
                <template slot-scope="scope">
                    <el-button size="mini" type="success" @click="handleDownload(scope.row)">下载</el-button>
                    <el-button size="mini" type="danger" @click="handleDelete(scope.row)" icon="el-icon-delete"
                        circle></el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- <el-pagination background layout="prev, pager, next,jumper, ->, total, slot" :total="fileList.length"
            @current-change="handleCurrentChange" :current-page="currentPage" :page-size="pageSize"
            style="text-align: right">
        </el-pagination> -->
    </div>
</template>
  
<script>
import request from '../api/request.js'
import {ipurl} from '../api/ipurl.js'
export default ({
    name: 'CodeBase',
    components: {
    },
    data() {
        return {
            uploadURL: ipurl + '/files/uploads',
            fileList: [],
            // currentPage: 1, //当前页 刷新后默认显示第一页
            // pageSize: 7, //每一页显示的数据量 此处每页显示7条数据
            Names: [],
            bucketName: { buckets: this.$cookies.getCookie("username") },
            multipleSelection: []
        };
    },
    //钩子函数，VUE对象初始化完成后自动执行
    created() {
        this.getAllFiles();
    },
    methods: {
        // //点击按钮切换页面
        // handleCurrentChange(currentPage) {
        //     this.currentPage = currentPage; //每次点击分页按钮，当前页发生变化
        //     // console.log(this.currentPage);
        // },
        //列表
        getAllFiles() {
            //发送ajax请求
            return request({
                method: 'get',
                url: "/files/filelist", params: { bucket: this.$cookies.getCookie("username") }
            }).then((res) => {
                this.fileList = res.data.data;
                // console.log(res.data);
                // console.log(this.bucketName);
            });
        },
        handleUploads1() {
            this.$message.success("上传成功！");
            this.getAllFiles();
        },
        handleUploads2() {
            this.$message.error("上传失败！");
            // this.getAllFiles();
        },

        handleDelete(row) {
            //console.log(this.fileList[index].objectName);
            this.$confirm('此操作将永久删除 ' + row.objectName + ' , 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                return request({
                    method: 'delete',
                    url: "/files/filedelete",
                    params: { bucket: this.$cookies.getCookie("username"), fileName: row.objectName }
                }).then((res) => {
                    if (res.data.code == 60061) {
                        this.$message.success(res.data.msg);
                    } else {
                        this.$message.error(res.data.msg);
                    }
                }).finally(() => {
                    this.getAllFiles();
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },
        handleDeletes() {
            //console.log(this.Names);
            this.$confirm('此操作将永久删除这些文件, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                return request({
                    method: 'delete',
                    url: "/files/filesdelete?bucket=" + this.$cookies.getCookie("username") + "&fileNames=" + this.Names
                }).then((res) => {
                    if (res.data.code == 60061) {
                        this.$message.success(res.data.msg);
                    } else {
                        this.$message.error(res.data.msg);
                    }
                    //console.log(res.data);
                }).finally(() => {
                    this.getAllFiles();
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },
        handleDownload(row) {
            //console.log(this.fileList[index].objectName);
            this.$message.success("即将开始下载 " + row.objectName + " ！");
            window.location.href = ipurl + '/files/download?fileName=' + row.objectName + "&bucket=" + this.$cookies.getCookie("username")
        },
        handleDownloads() {
            //console.log(this.Names);
            this.$message.success("即将开始打包下载！");
            window.location.href = ipurl + '/files/downloads?fileNames=' + this.Names + "&bucket=" + this.$cookies.getCookie("username")
        },

        toggleSelection(rows) {
            if (rows) {
                rows.forEach(row => {
                    this.$refs.multipleTable.toggleRowSelection(row);
                });
            } else {
                this.$refs.multipleTable.clearSelection();
            }
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
            let Names = [];
            let files = val;
            files.forEach(item => {
                Names.push(item.objectName)
            })
            //console.log(Names);
            this.Names = Names;
        },
    }
})

</script>
  
<style></style>
  