<template>
  <div>
    <el-divider>浏览记录</el-divider>
    <el-button plain size="mini" round @click="handleClear()" style="float: right;margin-right: 30px;">清空记录</el-button>
    <br>
    <div class="blocks">
      <el-timeline>
        <el-timeline-item :timestamp=record.create_time placement="top"
          v-for="record in recordForm.slice((currentPage - 1) * pageSize, currentPage * pageSize)" :key="record">

          <table class="alist_history">
            <tr>
              <td class="titles"><el-link :underline="false" style="font-size: 18px;font-weight: bold;"
                  @click="toArticleShow(record)">{{ record.editTitle }}</el-link></td>
            </tr>
            <tr>
              <td class="contents_history">{{ record.editContenthtml }}</td>
            </tr>
          </table>
        </el-timeline-item>
      </el-timeline>
    </div>
    <el-pagination background layout="prev, pager, next,jumper, ->, total, slot" :total="recordForm.length"
      @current-change="handleCurrentChange" :current-page="currentPage" :page-size="pageSize"
      style="text-align: right;margin-right: 30px;">
    </el-pagination>
  </div>
</template>
  
<script>
import request from '../api/request'
export default ({
  name: 'CodeUserHistory',
  components: {

  },
  data() {
    return {
      recordForm: {},
      currentPage: 1, //当前页 刷新后默认显示第一页
      pageSize: 5, //每一页显示的数据量 此处每页显示5条数据
    };
  },
  mounted() {
    this.getHistory();
  },
  methods: {
    //点击按钮切换页面
    handleCurrentChange(currentPage) {
      this.currentPage = currentPage; //每次点击分页按钮，当前页发生变化
      // console.log(this.currentPage);
    },
    toArticleShow(record) {
      // this.$message.success(article.id);
      // 跳转到新窗口
      let routeData = this.$router.resolve({ name: 'articleshow', params: { aid: record.article_id } });
      window.open(routeData.href, '_blank');
    },
    toUserIndex(article) {
      this.$message.success(article.username);
    },
    //列表
    getHistory() {
      //发送ajax请求
      return request({
        method: 'get',
        url: "/articlehistory/selectArticleHistorys", params: { username: this.$cookies.getCookie("username") }
      }).then((res) => {
        this.recordForm = res.data.data;
        for (var i = 0; i < this.recordForm.length; i++) {
          this.recordForm[i].editContenthtml = this.recordForm[i].editContent.replace(/<[^>]+>/g, "");
        }
        // console.log(this.recordForm);
      });
    },

    handleClear() {
      if(this.recordForm.length>0){
        this.$confirm('此操作将清空浏览记录, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        //发送ajax请求
        return request({
          method: 'delete',
          url: "/articlehistory/deleteArticleHistorys", params: { username: this.$cookies.getCookie("username") }
        }).then((res) => {
          if (res.data.code == 60371) {
            this.$message.info("已清空！");
            this.getHistory();
          } else if (res.data.code == 60370) {
            this.$message.error(res.data.msg);
          } else {
            this.$message.error(res.data.msg);
          }
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
      }else{
        this.$message.info("无浏览记录！");
      }
      
    },
  }
})
</script>
  
<style>
.blocks {
  width: 86%;
  margin-left: 4%;
}

.alist_history {
  width: 100%;
  border-radius: 5px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}

.contents_history {
  width: 98%;
  font-size: 15px;
  color: #3c3c3d;
  text-align: left;
  /* 溢出隐藏 */
  overflow: hidden;
  /* 溢出隐藏使用省略号代替 */
  text-overflow: ellipsis;
  /* 展示方式 */
  display: -webkit-box;
  /* 行数限制 */
  -webkit-line-clamp: 1;
  /* 从上往下书写 书写方向*/
  -webkit-box-orient: vertical;
  margin-top: 15px;
  margin-bottom: 15px;
  margin-left: 15px;
  margin-right: 15px;

}
</style>
  