<template>
    <div>
        <el-divider>待发布公告</el-divider>
        <el-table tooltip-effect="dark" max-height="575"
            :data="noticeList0.slice((currentPage - 1) * pageSize, currentPage * pageSize)"
            :default-sort="{ prop: 'createTime', order: 'descending' }">
            <el-table-column type="index" label="序号" width="55" align="center"></el-table-column>
            <el-table-column prop="id" label="公告ID" width="95" align="center">
            </el-table-column>
            <el-table-column prop="noticeTitle" label="标题" align="left" :show-overflow-tooltip="true">
            </el-table-column>
            <el-table-column prop="noticeContent" label="内容" align="left" :show-overflow-tooltip="true">
            </el-table-column>
            <el-table-column prop="createTime" label="创建日期" width="180" align="left">
            </el-table-column>
            <el-table-column label="操作" width="230" align="left">
                <template slot-scope="scope">
                    <el-button size="mini" type="info" icon="el-icon-view" @click="preview(scope.row)"></el-button>
                    <el-button size="mini" type="warning" icon="el-icon-s-promotion"
                        @click="handlePublishNotice(scope.row)"></el-button>
                    <el-button type="primary" size="mini" icon="el-icon-edit" @click="handleEditNotice(scope.row)"
                        round></el-button>
                    <el-button size="mini" type="danger" @click="handleDeleteNotice(scope.row)" icon="el-icon-delete"
                        circle></el-button>
                </template>
            </el-table-column>
        </el-table>
        <el-pagination background layout="prev, pager, next,jumper, ->, total, slot" :total="noticeList0.length"
            @current-change="handleCurrentChange" :current-page="currentPage" :page-size="pageSize"
            style="text-align: right">
        </el-pagination>
    </div>
</template>
  
<script>
import request from '../api/request'
export default ({
    name: 'CodeSuserUnNoticed',
    components: {
    },
    data() {
        return {
            noticeList0: [],
            currentPage: 1, //当前页 刷新后默认显示第一页
            pageSize: 10, //每一页显示的数据量 此处每页显示10条数据
        };
    },
    //钩子函数，VUE对象初始化完成后自动执行
    created() {
        this.getAllNotices0();
    },
    methods: {
        //点击按钮切换页面
        handleCurrentChange(currentPage) {
            this.currentPage = currentPage; //每次点击分页按钮，当前页发生变化
            // console.log(this.currentPage);
        },
        //重置表单
        resetForm() {
            this.noticeList0 = [];
        },
        //列表
        getAllNotices0() {
            //发送ajax请求
            return request({
                method: 'get',
                url: "/susers/notices/selectBySusername", params: { susername: this.$cookies.getCookie("susername"), status: "0" }
            }).then((res) => {
                this.resetForm();
                this.noticeList0 = res.data.data;
            });
        },
        handlePublishNotice(row) {

            //console.log(this.fileList[index].objectName);
            this.$confirm('此操作将立即发布ID为【' + row.id + '】的公告, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                return request({
                    method: 'get',
                    url: "/susers/notices/handleNotice",
                    params: { susername: this.$cookies.getCookie("susername"), id: row.id, status: "1" }
                }).then((res) => {
                    if (res.data.code == 60301) {
                        this.$message.success(res.data.msg);
                        this.getAllNotices0();
                    } else {
                        this.$message.error(res.data.msg);
                    }
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消'
                });
            });
        },
        handleDeleteNotice(row) {
            this.$confirm('此操作将永久删除ID为【' + row.id + '】的公告, 是否继续?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                return request({
                    method: 'delete',
                    url: "/susers/notices/deleteNoticeById",
                    params: { susername: this.$cookies.getCookie("susername"), id: row.id }
                }).then((res) => {
                    if (res.data.code == 60311) {
                        this.$message.success(res.data.msg);
                        this.getAllNotices0();
                    } else {
                        this.$message.error(res.data.msg);
                    }
                });
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });
        },
        preview(row) {
            this.$alert(row.noticeContent,row.noticeTitle, {
                dangerouslyUseHTMLString: true,
                confirmButtonText: '关闭',
                // lockScroll: false,
                closeOnClickModal: true
            });
        },

        handleEditNotice(row) {
            this.$router.push({ name: "editnoticebyid", params: { nid: row.id } });
        }
    }
})

</script>
  
<style></style>
  