//引入Vue核心库
import Vue from 'vue'
//引入Vuex
import Vuex from 'vuex'
//应用Vuex插件
Vue.use(Vuex)

//准备actions对象——响应组件中用户的动作
const actions = {
    //响应组件中加的动作
	patitle(context,value){
		// console.log('actions中的jia被调用了',context,value)
		context.commit('PTITLE',value)
	},
	pacontent(context,value){
		context.commit('PACONTENT',value)
	},
	codeselect(context,value){
		context.commit('CODESELECT',value)
	},
}
//准备mutations对象——修改state中的数据
const mutations = {
    //执行
	PTITLE(state,value){
		// console.log('mutations中的JIA被调用了',state,value)
		state.patitle = value
	},
	PACONTENT(state,value){
		state.pacontent = value
	},
	CODESELECT(state,value){
		state.codeselectby = value
	},
}
//准备state对象——保存具体的数据
const state = {
    patitle: 'null',
	pacontent: 'null',
	codeselectby: '',
}

//创建并暴露store
export default new Vuex.Store({
	actions,
	mutations,
	state
})