<template>
    <div id="indexnotice">
        <div class="card">
            <p class="card__title">{{ noticeList1[currentPage - 1].noticeTitle }}</p>
            <p class="card__content">{{ noticeList1[currentPage - 1].noticeContent }}</p>
            <div class="card__date">
                {{ noticeList1[currentPage - 1].createTime }}
            </div>
            <el-pagination small layout="prev, pager, next" :total="noticeList1.length"
                @current-change="handleCurrentChange" :current-page="currentPage" :page-size="pageSize">
            </el-pagination>
        </div>
    </div>
</template>
  
<script>
import request from '../api/request'
export default ({
    name: 'CodeIndexNotice',
    components: {

    },
    data() {
        return {
            noticeList1: {
                noticeTitle: '',
                noticeContent: '',
            },
            currentPage: 1, //当前页 刷新后默认显示第一页
            pageSize: 1, //每一页显示的数据量 此处每页显示10条数据
        };
    },
    //钩子函数，VUE对象初始化完成后自动执行
    mounted() {
        this.getAllNotices1();
    },
    methods: {
        //点击按钮切换页面
        handleCurrentChange(currentPage) {
            this.currentPage = currentPage; //每次点击分页按钮，当前页发生变化
            // console.log(this.currentPage);
        },
        //列表
        getAllNotices1() {
            //发送ajax请求
            return request({
                method: 'get',
                url:"/susers/notices/selectAllNotice"}).then((res) => {
                this.noticeList1 = res.data.data;
            });
        },

    }
})

</script>
  
<style>
#indexnotice {
    width: 320px;
    height: 100%;
    margin-top: 20px;
    margin-right: 55px;
    float: right;
}

.card {
    --border-radius: 0.75rem;
    --primary-color: #7257fa;
    --secondary-color: #3c3852;
    width: 320px;
    font-family: "Arial";
    padding: 1rem;
    cursor: pointer;
    border-radius: var(--border-radius);
    border: 1px solid #e8e8e8;
    transition: all .3s;
    box-shadow: 6px 6px 12px #c5c5c5,
        -6px -6px 12px #ffffff;
    position: relative;
}

/* .card>*+* {
    margin-top: 1.1em;
} */

.card__content {
    color: var(--secondary-color);
    font-size: 0.86rem;
    text-align: left;
}

.card__title {
    padding: 0;
    margin: 0;
    font-size: 1.5rem;
    font-weight: bold;
    text-align: center;
}

.card__date {
    color: #6e6b80;
    font-size: 0.8rem;
    text-align: right;
    margin-bottom: 5px;
}

#notice {
    font-size: 20px;
    font-weight: bold;
    text-align: center;
    color: #1e1e1e;
}
</style>
  