<template>
  <div>
    <el-divider>我的帖子</el-divider>
    <el-table tooltip-effect="dark" max-height="575"
      :data="articleList1.slice((currentPage - 1) * pageSize, currentPage * pageSize)"
      :default-sort="{ prop: 'createTime', order: 'descending' }">
      <el-table-column type="index" label="序号" width="55" align="center"></el-table-column>
      <el-table-column prop="id" label="帖子ID" width="95" align="center">
      </el-table-column>
      <el-table-column prop="editTitle" label="标题" align="left" :show-overflow-tooltip="true">
      </el-table-column>
      <el-table-column prop="editContenthtml" label="内容" align="left" :show-overflow-tooltip="true">
      </el-table-column>
      <el-table-column prop="createTime" label="发布日期" width="180" align="left">
      </el-table-column>
      <el-table-column label="操作" width="170" align="left">
        <template slot-scope="scope">
          <el-button size="mini" type="info" icon="el-icon-view" @click="preview(scope.row)"></el-button>
          <el-button size="mini" type="danger" @click="handleRevokeArticle(scope.row)" round>撤销发布</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination background layout="prev, pager, next,jumper, ->, total, slot" :total="articleList1.length"
      @current-change="handleCurrentChange" :current-page="currentPage" :page-size="pageSize" style="text-align: right">
    </el-pagination>
  </div>
</template>
  
<script>
import request from '../api/request'
export default ({
  name: 'CodeUserUpedArticle',
  components: {
  },
  data() {
    return {
      articleList1: [],
      currentPage: 1, //当前页 刷新后默认显示第一页
      pageSize: 10, //每一页显示的数据量 此处每页显示10条数据
      dialogTableVisible: false,
      htmls: ''
    };
  },
  //钩子函数，VUE对象初始化完成后自动执行
  mounted() {
    this.getAllFiles1();
  },
  methods: {
    //点击按钮切换页面
    handleCurrentChange(currentPage) {
      this.currentPage = currentPage; //每次点击分页按钮，当前页发生变化
      // console.log(this.currentPage);
    },
    //重置表单
    resetForm() {
      this.articleList1 = [];
    },
    //列表
    getAllFiles1() {
      //发送ajax请求
      return request({
        method: 'get',
        url: "/articles/selectByUsername", params: { username: this.$cookies.getCookie("username"), status: "1" }
      }).then((res) => {
        this.resetForm();
        this.articleList1 = res.data.data;
        for (var i = 0; i < this.articleList1.length; i++) {
          this.articleList1[i].editContenthtml = this.articleList1[i].editContent.replace(/<[^>]+>/g, "");
        }
      });
    },
    preview(row) {
      // this.$alert(row.editContent, row.editTitle, {
      //   dangerouslyUseHTMLString: true,
      //   confirmButtonText: '关闭',
      //   // lockScroll: false,
      //   closeOnClickModal: true,
      //   customClass:'message_box_preview'
      // });
      // this.$router.push({ name: "articlepreviewbyid", params: { paid: row.id } });
      sessionStorage.setItem('ptitle',row.editTitle);
      sessionStorage.setItem('pcontent',row.editContent);
      this.$router.push({ name: "articlepreview"});
    },
    handleRevokeArticle(row) {
      //console.log(this.fileList[index].objectName);
      this.$confirm('此操作将撤销ID为【' + row.id + '】的贴子的发布, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        return request({
          method: 'get',
          url: "/articles/handleArticle",
          params: { username: this.$cookies.getCookie("username"), id: row.id, status: "0" }
        }).then((res) => {
          if (res.data.code == 60141) {
            this.$message.success(res.data.msg);
            this.getAllFiles1();
          } else {
            this.$message.error(res.data.msg);
          }
        });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消'
        });
      });
    },
  }
})

</script>
  
<style>
.message_box_preview {
    width: 800px;
}
</style>
  