<template>
    <div>
        <table class="indexselect">
            <tr>
                <td width="5.5%"></td>
                <td align="left" width="76.5%">
                    <input class="intext" type="text" placeholder="请输入要查询的内容" clearable v-model="selectInfo">
                    <button class="inselect" type="submit" @click="selectByInfo()">查一下</button>
                </td>
                <td width="18%"></td>
            </tr>
        </table>
    </div>
</template>
  
<script>
export default ({
    name: 'CodeSelectBar',
    data() {
        return {
            selectInfo: '',
        }
    },
    mounted(){
        if(sessionStorage.getItem("selectInfo")!=null){
            this.selectInfo = sessionStorage.getItem("selectInfo");
        }else{
            this.selectInfo = this.$route.params.sby;
        }
    },
    methods: {
        selectByInfo() {
            sessionStorage.setItem("selectInfo",this.selectInfo);
            this.$router.go(0);
        }
    }
})

</script>
  
<style>
.indexselect {
    /* 搜索栏 */
    width: 90%;
    height: 55px;
    min-width: 1280px;
    float: left;
    padding-left: 5%;
    padding-right: 5%;
    border-collapse: collapse;
}

.inselect {
    width: 20%;
    height: 44px;
    text-align: center;
    border: 0px;
    font-size: 20px;
    font-weight: bold;
    color: whitesmoke;
    float: left;
    margin-top: 2%;
    background-color: #fe6b00;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    cursor: pointer;
}

.intext {
    height: 42px;
    width: 75%;
    text-align: left;
    border: 0px;
    float: left;
    margin-top: 2%;
    padding-left: 15px;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    background-color: antiquewhite;
}
</style>
  